// 
export const getSortedPlatforms = (language: 'zh' | 'en'): SocialPlatform[] => {
  return [...socialPlatforms].sort((a, b) => {
    return a.priority[language] - b.priority[language];
  });
};

// 
export const getPaginatedPlatforms = (visibleCount: number, language: 'zh' | 'en' = 'zh'): SocialPlatform[] => {
  const sortedPlatforms = getSortedPlatforms(language);
  return sortedPlatforms.slice(0, Math.min(visibleCount, sortedPlatforms.length));
}; 