import React, { useState, useEffect, useCallback } from 'react';
import { 
  Grid, 
  TextField, 
  Typography, 
  Button, 
  Box,
  InputAdornment,
  Paper,
  Tooltip,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  SelectChangeEvent
} from '@mui/material';
import RefreshIcon from '@mui/icons-material/Refresh';
import { SocialMediaData, ContactInfo, UserData } from '../types/index';
import { socialPlatforms, getPaginatedPlatforms, getSortedPlatforms } from '../assets/socialPlatforms';
import { useThemeContext } from '../contexts/ThemeContext';

interface MediaFormProps {
  userData: UserData;
  onUpdateUserData: (data: UserData) => void;
}

const initialSocialMediaData: SocialMediaData = {
  // 
  wechat: null,
  douyin: null,
  xiaohongshu: null,
  weibo: null,
  bilibili: null,
  videoAccount: null,
  kuaishou: null,
  zhihu: null,
  juejin: null,
  
  // 
  twitter: null,
  tiktok: null,
  facebook: null,
  instagram: null,
  youtube: null,
  linkedin: null,
  reddit: null,
  discord: null,
  telegram: null,
  snapchat: null
};

const initialContactInfo: ContactInfo = {
  wechatId: '',
  email: '',
  twitter: '',
  facebook: ''
};

const MediaForm: React.FC<MediaFormProps> = React.memo(({ userData, onUpdateUserData }) => {
  const { mode, t, language } = useThemeContext();
  const isDarkMode = mode === 'dark';
  
  const [socialMedia, setSocialMedia] = useState<SocialMediaData>(
    userData.socialMedia || initialSocialMediaData
  );
  const [contactInfo, setContactInfo] = useState<ContactInfo>(
    userData.contactInfo || initialContactInfo
  );
  // 
  const [contactType, setContactType] = useState<'wechat' | 'email' | 'twitter' | 'facebook' | 'weibo' | 'douyin' | 'xiaohongshu' | 'discord' | 'reddit' | 'tiktok' | 'telegram'>('wechat');
  const [localWechatId, setLocalWechatId] = useState(userData.contactInfo?.wechatId || '');
  const [localEmail, setLocalEmail] = useState(userData.contactInfo?.email || '');
  const [localTwitter, setLocalTwitter] = useState(userData.contactInfo?.twitter || '');
  const [localFacebook, setLocalFacebook] = useState(userData.contactInfo?.facebook || '');
  const [localWeibo, setLocalWeibo] = useState(userData.contactInfo?.weibo || '');
  const [localDouyin, setLocalDouyin] = useState(userData.contactInfo?.douyin || '');
  const [localXiaohongshu, setLocalXiaohongshu] = useState(userData.contactInfo?.xiaohongshu || '');
  const [localDiscord, setLocalDiscord] = useState(userData.contactInfo?.discord || '');
  const [localReddit, setLocalReddit] = useState(userData.contactInfo?.reddit || '');
  const [localTiktok, setLocalTiktok] = useState(userData.contactInfo?.tiktok || '');
  const [localTelegram, setLocalTelegram] = useState(userData.contactInfo?.telegram || '');
  
  // localSocialMedia
  const initializeLocalSocialMedia = (socialMediaData: SocialMediaData): {[key: string]: string} => {
    const result: {[key: string]: string} = {};
    Object.entries(socialMediaData).forEach(([key, value]) => {
      result[key] = value === null ? '' : String(value);
    });
    return result;
  };
  
  const [localSocialMedia, setLocalSocialMedia] = useState<{[key: string]: string}>(
    initializeLocalSocialMedia(userData.socialMedia || initialSocialMediaData)
  );
  const [visiblePlatformsCount, setVisiblePlatformsCount] = useState<number>(9);

  // userData
  useEffect(() => {
    if (userData) {
      setSocialMedia(userData.socialMedia || initialSocialMediaData);
      setContactInfo(userData.contactInfo || initialContactInfo);
      setLocalWechatId(userData.contactInfo?.wechatId || '');
      setLocalEmail(userData.contactInfo?.email || '');
      setLocalTwitter(userData.contactInfo?.twitter || '');
      setLocalFacebook(userData.contactInfo?.facebook || '');
      setLocalWeibo(userData.contactInfo?.weibo || '');
      setLocalDouyin(userData.contactInfo?.douyin || '');
      setLocalXiaohongshu(userData.contactInfo?.xiaohongshu || '');
      setLocalDiscord(userData.contactInfo?.discord || '');
      setLocalReddit(userData.contactInfo?.reddit || '');
      setLocalTiktok(userData.contactInfo?.tiktok || '');
      setLocalTelegram(userData.contactInfo?.telegram || '');
      
      // 
      setLocalSocialMedia(initializeLocalSocialMedia(userData.socialMedia || initialSocialMediaData));
    }
  }, [userData]);

  // useCallback
  const updateUserData = useCallback(() => {
    onUpdateUserData({
      socialMedia,
      contactInfo,
      updatedAt: new Date()
    });
  }, [socialMedia, contactInfo, onUpdateUserData]);

  // 
  useEffect(() => {
    const timer = setTimeout(() => {
      updateUserData();
    }, 500); // 500ms

    return () => clearTimeout(timer);
  }, [socialMedia, contactInfo, updateUserData]);

  const handleSocialMediaChange = (platform: string, value: string) => {
    // 
    setLocalSocialMedia((prev: {[key: string]: string}) => ({
      ...prev,
      [platform]: value
    }));
    
    // 
    const numValue = value === '' ? null : Number(value);
    setSocialMedia((prev: SocialMediaData) => ({
      ...prev,
      [platform]: numValue
    }));
  };

  const handleContactInfoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (contactType === 'wechat') {
      setLocalWechatId(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        wechatId: e.target.value
      }));
    } else if (contactType === 'email') {
      setLocalEmail(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        email: e.target.value
      }));
    } else if (contactType === 'twitter') {
      setLocalTwitter(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        twitter: e.target.value
      }));
    } else if (contactType === 'facebook') {
      setLocalFacebook(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        facebook: e.target.value
      }));
    } else if (contactType === 'weibo') {
      setLocalWeibo(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        weibo: e.target.value
      }));
    } else if (contactType === 'douyin') {
      setLocalDouyin(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        douyin: e.target.value
      }));
    } else if (contactType === 'xiaohongshu') {
      setLocalXiaohongshu(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        xiaohongshu: e.target.value
      }));
    } else if (contactType === 'discord') {
      setLocalDiscord(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        discord: e.target.value
      }));
    } else if (contactType === 'reddit') {
      setLocalReddit(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        reddit: e.target.value
      }));
    } else if (contactType === 'tiktok') {
      setLocalTiktok(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        tiktok: e.target.value
      }));
    } else if (contactType === 'telegram') {
      setLocalTelegram(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        telegram: e.target.value
      }));
    }
  };

  const handleContactTypeChange = (e: SelectChangeEvent) => {
    const newContactType = e.target.value as 'wechat' | 'email' | 'twitter' | 'facebook' | 'weibo' | 'douyin' | 'xiaohongshu' | 'discord' | 'reddit' | 'tiktok' | 'telegram';
    
    // 
    if (contactType === 'wechat') {
      setLocalWechatId('');
      setContactInfo(prev => ({ ...prev, wechatId: '' }));
    } else if (contactType === 'email') {
      setLocalEmail('');
      setContactInfo(prev => ({ ...prev, email: '' }));
    } else if (contactType === 'twitter') {
      setLocalTwitter('');
      setContactInfo(prev => ({ ...prev, twitter: '' }));
    } else if (contactType === 'facebook') {
      setLocalFacebook('');
      setContactInfo(prev => ({ ...prev, facebook: '' }));
    } else if (contactType === 'weibo') {
      setLocalWeibo('');
      setContactInfo(prev => ({ ...prev, weibo: '' }));
    } else if (contactType === 'douyin') {
      setLocalDouyin('');
      setContactInfo(prev => ({ ...prev, douyin: '' }));
    } else if (contactType === 'xiaohongshu') {
      setLocalXiaohongshu('');
      setContactInfo(prev => ({ ...prev, xiaohongshu: '' }));
    } else if (contactType === 'discord') {
      setLocalDiscord('');
      setContactInfo(prev => ({ ...prev, discord: '' }));
    } else if (contactType === 'reddit') {
      setLocalReddit('');
      setContactInfo(prev => ({ ...prev, reddit: '' }));
    } else if (contactType === 'tiktok') {
      setLocalTiktok('');
      setContactInfo(prev => ({ ...prev, tiktok: '' }));
    } else if (contactType === 'telegram') {
      setLocalTelegram('');
      setContactInfo(prev => ({ ...prev, telegram: '' }));
    }
    
    setContactType(newContactType);
  };

  const handleReset = () => {
    setSocialMedia(initialSocialMediaData);
    setContactInfo(initialContactInfo);
    setLocalWechatId('');
    setLocalEmail('');
    setLocalTwitter('');
    setLocalFacebook('');
    setLocalWeibo('');
    setLocalDouyin('');
    setLocalXiaohongshu('');
    setLocalDiscord('');
    setLocalReddit('');
    setLocalTiktok('');
    setLocalTelegram('');
  };

  const handleShowMorePlatforms = () => {
    setVisiblePlatformsCount(prev => prev + 6);
  };

  // 
  const visiblePlatforms = getPaginatedPlatforms(visiblePlatformsCount, language);
  // 
  const sortedPlatforms = getSortedPlatforms(language);
  const hasMorePlatforms = visiblePlatformsCount < sortedPlatforms.length;

  // 
  const getContactOptions = () => {
    if (language === 'zh') {
      // 
      return [
        { value: 'wechat', label: t('wechatId'), icon: 'wechat.png' },
        { value: 'weibo', label: t('weibo'), icon: 'weibo.png' },
        { value: 'douyin', label: t('douyin'), icon: 'douyin.png' },
        { value: 'xiaohongshu', label: t('xiaohongshu'), icon: 'xiaohongshu.png' },
        { value: 'email', label: t('email'), icon: 'mail.png' },
        { value: 'twitter', label: t('twitter'), icon: 'twitter.png' },
        { value: 'facebook', label: t('facebook'), icon: 'facebook.png' },
        { value: 'tiktok', label: t('tiktok'), icon: 'tiktok.png' },
        { value: 'discord', label: t('discord'), icon: 'discord.png' },
        { value: 'reddit', label: t('reddit'), icon: 'reddit.png' },
        { value: 'telegram', label: t('telegram'), icon: 'telegram.png' }
      ];
    } else {
      // 
      return [
        { value: 'email', label: t('email'), icon: 'mail.png' },
        { value: 'twitter', label: t('twitter'), icon: 'twitter.png' },
        { value: 'facebook', label: t('facebook'), icon: 'facebook.png' },
        { value: 'tiktok', label: t('tiktok'), icon: 'tiktok.png' },
        { value: 'discord', label: t('discord'), icon: 'discord.png' },
        { value: 'reddit', label: t('reddit'), icon: 'reddit.png' },
        { value: 'telegram', label: t('telegram'), icon: 'telegram.png' },
        { value: 'wechat', label: t('wechatId'), icon: 'wechat.png' },
        { value: 'weibo', label: t('weibo'), icon: 'weibo.png' },
        { value: 'douyin', label: t('douyin'), icon: 'douyin.png' },
        { value: 'xiaohongshu', label: t('xiaohongshu'), icon: 'xiaohongshu.png' }
      ];
    }
  };

  return (
    <Paper 
      elevation={3} 
      sx={{ 
        p: 3, 
        borderRadius: '12px',
        backgroundColor: isDarkMode ? 'rgba(255,255,255,0.05)' : '#ffffff',
        border: '1px solid',
        borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.08)',
        boxShadow: isDarkMode 
          ? '0 8px 20px rgba(0,0,0,0.2)' 
          : '0 10px 25px rgba(0,0,0,0.08)',
        transition: 'all 0.3s ease'
      }}
    >
      <Typography 
        variant="h5" 
        gutterBottom 
        sx={{ 
          fontWeight: 600, 
          color: isDarkMode ? '#fff' : '#1a1a2e',
          borderBottom: '2px solid',
          borderColor: 'primary.main',
          pb: 1,
          mb: 3
        }}
      >
        {t('inputYourData')}
      </Typography>
      
      <Grid container spacing={3}>
        {visiblePlatforms.map(platform => (
          <Grid item xs={12} sm={6} md={4} key={platform.id}>
            <TextField
              fullWidth
              label={platform.name}
              type="number"
              placeholder={t('fansPlaceholder')}
              value={localSocialMedia[platform.id] || ''}
              onChange={(e) => handleSocialMediaChange(platform.id, e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Box 
                      component="img"
                      sx={{ 
                        width: 20, 
                        height: 20,
                        mr: 1,
                        objectFit: 'contain',
                        transition: 'transform 0.3s ease',
                        '&:hover': {
                          transform: 'scale(1.15)'
                        }
                      }}
                      alt={platform.name}
                      src={platform.pngPath}
                      onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
                        const target = e.target as HTMLImageElement;
                        target.onerror = null;
                        target.src = platform.iconPath;
                      }}
                    />
                  </InputAdornment>
                ),
                endAdornment: <InputAdornment position="end">{t('followers')}</InputAdornment>,
                sx: {
                  borderRadius: '10px',
                  transition: 'all 0.3s ease',
                  '& .MuiOutlinedInput-notchedOutline': {
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.2)',
                    transition: 'border-color 0.3s ease'
                  },
                  '&:hover .MuiOutlinedInput-notchedOutline': {
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.3)' : 'rgba(0,0,0,0.3)',
                    boxShadow: '0 0 8px rgba(0,0,0,0.05)'
                  },
                  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                    borderColor: platform.color,
                    boxShadow: `0 0 0 2px ${platform.color}25`
                  },
                  '&:hover': {
                    transform: 'translateY(-2px)'
                  }
                }
              }}
              inputProps={{
                min: 0,
                max: 999999999,
              }}
              sx={{
                '& .MuiInputLabel-root': {
                  color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)'
                },
                '& .MuiInputLabel-root.Mui-focused': {
                  color: platform.color
                }
              }}
            />
          </Grid>
        ))}
      </Grid>
      
      {hasMorePlatforms && (
        <Box 
          sx={{ 
            display: 'flex', 
            justifyContent: 'center', 
            mt: 4,
            mb: 2 
          }}
        >
          <Button
            variant="outlined"
            color="primary"
            onClick={handleShowMorePlatforms}
            startIcon={<Box sx={{ display: 'flex', alignItems: 'center' }}>+</Box>}
            sx={{
              borderRadius: '20px',
              px: 3,
              py: 1,
              borderWidth: '2px',
              fontWeight: 500,
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-2px)',
                boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
              }
            }}
          >
            {t('morePlatforms')} ({socialPlatforms.length - visiblePlatformsCount}{t('remaining')})
          </Button>
        </Box>
      )}
      
      <Box sx={{ mt: 6, mb: 3 }}>
        <Typography 
          variant="h6" 
          gutterBottom 
          sx={{ 
            fontWeight: 600, 
            color: isDarkMode ? '#fff' : '#1a1a2e',
            borderBottom: '1px solid',
            borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)',
            pb: 1,
            mb: 3
          }}
        >
          {t('contactWay')}
        </Typography>
        
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12}>
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column',
              gap: 2,
              position: 'relative',
              transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)'
            }}>
              <FormControl sx={{ 
                position: 'relative',
                boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                borderRadius: '12px',
                border: '1px solid',
                borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                p: 2,
                transform: 'translateY(0)',
                transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: '0 8px 16px rgba(0,0,0,0.12)'
                }
              }}>
                <InputLabel id="contact-type-label" sx={{ 
                  position: 'relative', 
                  transform: 'none',
                  color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)',
                  fontSize: '0.9rem',
                  mb: 1,
                  fontWeight: 500
                }}>{t('contactInfoType')}</InputLabel>
                <Select
                  labelId="contact-type-label"
                  value={contactType}
                  onChange={handleContactTypeChange}
                  sx={{
                    '& .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    '&:hover .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    '&.Mui-focused .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    bgcolor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'white',
                    borderRadius: '8px',
                    boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                    fontWeight: 500
                  }}
                >
                  {getContactOptions().map(option => (
                    <MenuItem key={option.value} value={option.value}>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        <Box 
                          component="img"
                          src={`${process.env.PUBLIC_URL}/icons/${option.icon}`}
                          alt={option.label}
                          style={{ width: 20, height: 20 }}
                        />
                        {option.label}
                      </Box>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              
              <Box sx={{
                position: 'relative',
                borderRadius: '12px',
                boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                border: '1px solid',
                borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                p: 2,
                mt: 1,
                transform: 'translateY(0)',
                transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: '0 8px 16px rgba(0,0,0,0.12)'
                }
              }}>
                <TextField
                  fullWidth
                  label={contactType === 'wechat' ? t('wechatId') : contactType === 'email' ? t('email') : contactType === 'twitter' ? t('twitter') : contactType === 'facebook' ? t('facebook') : contactType === 'weibo' ? t('weibo') : contactType === 'douyin' ? t('douyin') : contactType === 'xiaohongshu' ? t('xiaohongshu') : contactType === 'discord' ? t('discord') : contactType === 'reddit' ? t('reddit') : contactType === 'tiktok' ? t('tiktok') : t('telegram')}
                  placeholder={contactType === 'wechat' 
                    ? t('wechatPlaceholder')
                    : contactType === 'email' ? t('emailPlaceholder') : contactType === 'twitter' ? t('twitterPlaceholder') : contactType === 'facebook' ? t('facebookPlaceholder') : contactType === 'weibo' ? t('weiboPlaceholder') : contactType === 'douyin' ? t('douyinPlaceholder') : contactType === 'xiaohongshu' ? t('xiaohongshuPlaceholder') : contactType === 'discord' ? t('discordPlaceholder') : contactType === 'reddit' ? t('redditPlaceholder') : contactType === 'tiktok' ? t('tiktokPlaceholder') : t('telegramPlaceholder')}
                  value={contactType === 'wechat' ? localWechatId : contactType === 'email' ? localEmail : contactType === 'twitter' ? localTwitter : contactType === 'facebook' ? localFacebook : contactType === 'weibo' ? localWeibo : contactType === 'douyin' ? localDouyin : contactType === 'xiaohongshu' ? localXiaohongshu : contactType === 'discord' ? localDiscord : contactType === 'reddit' ? localReddit : contactType === 'tiktok' ? localTiktok : localTelegram}
                  onChange={handleContactInfoChange}
                  variant="standard"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                          <Box 
                            component="img"
                            sx={{ 
                              width: 24, 
                              height: 24,
                              mr: 1
                            }}
                          alt={getContactOptions().find(option => option.value === contactType)?.label || ''}
                          src={`${process.env.PUBLIC_URL}/icons/${getContactOptions().find(option => option.value === contactType)?.icon || 'wechat.png'}`}
                            onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
                              const target = e.target as HTMLImageElement;
                              target.onerror = null;
                            // pngsvg
                            const iconName = getContactOptions().find(option => option.value === contactType)?.icon?.replace('.png', '.svg') || 'wechat.svg';
                            target.src = `${process.env.PUBLIC_URL}/icons/${iconName}`;
                            }}
                          />
                      </InputAdornment>
                    ),
                    disableUnderline: true,
                    sx: {
                      px: 2,
                      py: 1,
                      bgcolor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'white',
                      borderRadius: '8px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                      '&:hover': {
                        bgcolor: isDarkMode ? 'rgba(255,255,255,0.12)' : 'rgba(242,242,242,1)'
                      },
                      transition: 'background-color 0.3s ease'
                    }
                  }}
                  sx={{ 
                    '& .MuiFormLabel-root': {
                      color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)',
                      fontSize: '0.9rem',
                      transform: 'translate(0, -12px) scale(1)',
                      fontWeight: 500
                    },
                    '& .MuiInputLabel-shrink': {
                      transform: 'translate(0, -12px) scale(1)'
                    }
                  }}
                />
              </Box>
            </Box>
          </Grid>
        </Grid>
        
        <Tooltip title={t('resetAll')}>
          <Button 
            variant="outlined" 
            color="error" 
            onClick={handleReset}
            startIcon={<RefreshIcon />}
            sx={{ 
              minWidth: '100px',
              height: '46px',
              borderRadius: '8px',
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-2px)',
                boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
              }
            }}
          >
            {t('resetData')}
          </Button>
        </Tooltip>
      </Box>
    </Paper>
  );
});

export default MediaForm; 