import React from 'react';
import {
  Box,
  Typography,
  Slider,
  Grid,
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails
} from '@mui/material';
import {
  ExpandMore as ExpandMoreIcon,
  Refresh as RefreshIcon
} from '@mui/icons-material';
import { ImageEffects } from '../../types/index';
import { createDefaultImageEffects, isDefaultEffects } from '../../utils/imageProcessor';
import { useThemeContext } from '../../contexts/ThemeContext';

interface ImageEffectsProps {
  effects: ImageEffects;
  onChange: (effects: ImageEffects) => void;
  disabled?: boolean;
}

const ImageEffectsPanel: React.FC<ImageEffectsProps> = ({
  effects,
  onChange,
  disabled = false
}) => {
  const handleEffectChange = (effectName: keyof ImageEffects, value: number) => {
    onChange({
      ...effects,
      [effectName]: value
    });
  };

  const handleReset = () => {
    onChange(createDefaultImageEffects());
  };

  const { t } = useThemeContext();

  const effectConfigs = [
    {
      key: 'blur' as keyof ImageEffects,
      label: t('blur') || '',
      min: 0,
      max: 10,
      step: 0.5,
      unit: 'px',
      description: t('blurDescription') || ''
    },
    {
      key: 'brightness' as keyof ImageEffects,
      label: t('brightness') || '',
      min: 0,
      max: 2,
      step: 0.1,
      unit: '',
      description: t('brightnessDescription') || '1'
    },
    {
      key: 'contrast' as keyof ImageEffects,
      label: t('contrast') || '',
      min: 0,
      max: 2,
      step: 0.1,
      unit: '',
      description: t('contrastDescription') || '1'
    },
    {
      key: 'saturation' as keyof ImageEffects,
      label: t('saturation') || '',
      min: 0,
      max: 2,
      step: 0.1,
      unit: '',
      description: t('saturationDescription') || '1'
    },
    {
      key: 'sepia' as keyof ImageEffects,
      label: t('sepia') || '',
      min: 0,
      max: 1,
      step: 0.1,
      unit: '',
      description: t('sepiaDescription') || '0'
    }
  ];

  const formatValue = (value: number, unit: string): string => {
    if (unit === 'px') {
      return `${value}${unit}`;
    }
    if (unit === '') {
      return value.toFixed(1);
    }
    return `${Math.round(value * 100)}%`;
  };

  return (
    <Accordion defaultExpanded={false}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="image-effects-content"
        id="image-effects-header"
      >
        <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
          <Typography variant="body2" sx={{ flexGrow: 1 }}>
            {t('imageEffectsTitle') || ''}
          </Typography>
          {!isDefaultEffects(effects) && (
            <Typography variant="caption" color="primary">
              
            </Typography>
          )}
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <Box>
          <Grid container spacing={3}>
            {effectConfigs.map((config) => (
              <Grid item xs={12} key={config.key}>
                <Box>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                    <Typography variant="body2" sx={{ flexGrow: 1 }}>
                      {config.label}
                    </Typography>
                    <Typography 
                      variant="caption" 
                      color="text.secondary"
                      sx={{ 
                        minWidth: '48px', 
                        textAlign: 'right',
                        fontFamily: 'monospace'
                      }}
                    >
                      {formatValue(effects[config.key], config.unit)}
                    </Typography>
                  </Box>
                  
                  <Slider
                    value={effects[config.key]}
                    onChange={(_, value) => handleEffectChange(config.key, value as number)}
                    min={config.min}
                    max={config.max}
                    step={config.step}
                    disabled={disabled}
                    sx={{
                      '& .MuiSlider-thumb': {
                        width: 16,
                        height: 16
                      },
                      '& .MuiSlider-track': {
                        height: 4
                      },
                      '& .MuiSlider-rail': {
                        height: 4
                      }
                    }}
                  />
                  
                  <Typography 
                    variant="caption" 
                    color="text.secondary"
                    sx={{ mt: 0.5, display: 'block', fontSize: '0.7rem' }}
                  >
                    {config.description}
                  </Typography>
                </Box>
              </Grid>
            ))}
            
            {/*  */}
            <Grid item xs={12}>
              <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center' }}>
                <Button
                  variant="outlined"
                  size="small"
                  startIcon={<RefreshIcon />}
                  onClick={handleReset}
                  disabled={disabled || isDefaultEffects(effects)}
                  sx={{ textTransform: 'none' }}
                >
                  {t('resetEffects') || ''}
                </Button>
              </Box>
            </Grid>
            
            {/*  */}
            <Grid item xs={12}>
              <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                {t('effectsPresets') || ''}
              </Typography>
              <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
                <Button
                  variant="outlined"
                  size="small"
                  disabled={disabled}
                  onClick={() => onChange({
                    blur: 0,
                    brightness: 1.2,
                    contrast: 1.1,
                    saturation: 1.2,
                    sepia: 0
                  })}
                  sx={{ textTransform: 'none', fontSize: '0.7rem' }}
                >
                  {t('brightEffect') || ''}
                </Button>
                <Button
                  variant="outlined"
                  size="small"
                  disabled={disabled}
                  onClick={() => onChange({
                    blur: 0,
                    brightness: 0.8,
                    contrast: 1.2,
                    saturation: 0.8,
                    sepia: 0
                  })}
                  sx={{ textTransform: 'none', fontSize: '0.7rem' }}
                >
                  {t('darkEffect') || ''}
                </Button>
                <Button
                  variant="outlined"
                  size="small"
                  disabled={disabled}
                  onClick={() => onChange({
                    blur: 0,
                    brightness: 1.1,
                    contrast: 0.9,
                    saturation: 0.7,
                    sepia: 0.3
                  })}
                  sx={{ textTransform: 'none', fontSize: '0.7rem' }}
                >
                  {t('vintageEffect') || ''}
                </Button>
                <Button
                  variant="outlined"
                  size="small"
                  disabled={disabled}
                  onClick={() => onChange({
                    blur: 2,
                    brightness: 1,
                    contrast: 1,
                    saturation: 1,
                    sepia: 0
                  })}
                  sx={{ textTransform: 'none', fontSize: '0.7rem' }}
                >
                  {t('softFocusEffect') || ''}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </AccordionDetails>
    </Accordion>
  );
};

export default ImageEffectsPanel; 