import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { GlobalStyles } from '@mui/material';
import en from '../locales/en';
import zh from '../locales/zh';

type ThemeMode = 'light' | 'dark';
type Language = 'en' | 'zh';

interface ThemeContextType {
  mode: ThemeMode;
  language: Language;
  toggleTheme: () => void;
  setLanguage: (lang: Language) => void;
  t: (key: string) => string;
}

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

const getInitialLanguage = (): Language => {
  // 1. localStorage
  const storedLanguage = localStorage.getItem('preferred-language') as Language;
  if (storedLanguage && ['zh', 'en'].includes(storedLanguage)) {
    return storedLanguage;
  }

  // 2. 
  const primaryLang = navigator.language.toLowerCase();
  
  // 3. 
  const chineseVariants = ['zh', 'zh-cn', 'zh-tw', 'zh-hk', 'zh-sg'];
  if (chineseVariants.some(variant => primaryLang.startsWith(variant))) {
    return 'zh';
  }

  // 4. 
  if (navigator.languages) {
    for (const lang of navigator.languages) {
      const langLower = lang.toLowerCase();
      if (chineseVariants.some(variant => langLower.startsWith(variant))) {
        return 'zh';
      }
    }
  }

  // 5. 
  return 'en';
};

const getInitialTheme = (): ThemeMode => {
  // localStorage
  const storedMode = localStorage.getItem('theme-mode') as ThemeMode;
  if (storedMode && ['light', 'dark'].includes(storedMode)) {
    return storedMode;
  }
  
  // 
  const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
  const detectedMode: ThemeMode = prefersDark ? 'dark' : 'light';
  return detectedMode;
};

export const ThemeContextProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [mode, setMode] = useState<ThemeMode>(getInitialTheme);
  const [language, setLanguageState] = useState<Language>(getInitialLanguage);

  // 
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    const handleChange = (e: MediaQueryListEvent) => {
      const newMode: ThemeMode = e.matches ? 'dark' : 'light';
      setMode(newMode);
      localStorage.setItem('theme-mode', newMode);
    };

    mediaQuery.addListener(handleChange);
    return () => mediaQuery.removeListener(handleChange);
  }, []);

  const toggleTheme = () => {
    const newMode: ThemeMode = mode === 'light' ? 'dark' : 'light';
    setMode(newMode);
    localStorage.setItem('theme-mode', newMode);
  };

  const setLanguage = (lang: Language) => {
    setLanguageState(lang);
    localStorage.setItem('preferred-language', lang);
  };

  const getTranslations = (lang: Language) => {
    return lang === 'zh' ? zh : en;
  };

  const t = (key: string): string => {
    const translations = getTranslations(language);
    return translations[key as keyof typeof translations] || key;
  };

  const theme = createTheme({
    palette: {
      mode,
      primary: {
        main: mode === 'dark' ? '#90caf9' : '#1976d2',
      },
      secondary: {
        main: mode === 'dark' ? '#f48fb1' : '#dc004e',
      },
      background: {
        default: mode === 'dark' ? '#0a0a0a' : '#fafafa',
        paper: mode === 'dark' ? '#1a1a1a' : '#ffffff',
      },
    },
    typography: {
      fontFamily: [
        '-apple-system',
        'BlinkMacSystemFont',
        '"Segoe UI"',
        'Roboto',
        '"Helvetica Neue"',
        'Arial',
        'sans-serif',
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"',
      ].join(','),
    },
    components: {
      MuiButton: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            borderRadius: 8,
          },
        },
      },
      MuiPaper: {
        styleOverrides: {
          root: {
            backgroundImage: 'none',
          },
        },
      },
    },
  });

  return (
    <ThemeContext.Provider value={{ mode, language, toggleTheme, setLanguage, t }}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <GlobalStyles
          styles={{
            '*': {
              scrollbarWidth: 'thin',
              scrollbarColor: mode === 'dark' ? '#6b6b6b #2b2b2b' : '#c1c1c1 #f1f1f1',
            },
            '*::-webkit-scrollbar': {
              width: '8px',
            },
            '*::-webkit-scrollbar-track': {
              background: mode === 'dark' ? '#2b2b2b' : '#f1f1f1',
            },
            '*::-webkit-scrollbar-thumb': {
              backgroundColor: mode === 'dark' ? '#6b6b6b' : '#c1c1c1',
              borderRadius: '20px',
              border: `3px solid ${mode === 'dark' ? '#2b2b2b' : '#f1f1f1'}`,
            },
          }}
        />
        {children}
      </ThemeProvider>
    </ThemeContext.Provider>
  );
};

export const useThemeContext = (): ThemeContextType => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useThemeContext must be used within a ThemeContextProvider');
  }
  return context;
}; 