import { 
  ProcessingOptions, 
  Size, 
  ScaleInfo, 
  ProcessingResult, 
  ImageEffects 
} from '../types/index';

/**
 * 
 */
export class ImageProcessor {
  /**
   * 
   * @param imageFile 
   * @param targetWidth 
   * @param targetHeight 
   * @param options 
   * @returns 
   */
  static async processImage(
    imageFile: File,
    targetWidth: number,
    targetHeight: number,
    options: ProcessingOptions
  ): Promise<ProcessingResult> {
    
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d')!;
    
    // 1. 
    const image = await this.loadImage(imageFile);
    
    // 2. 
    const scaleInfo = this.calculateOptimalScale(
      { width: image.width, height: image.height },
      { width: targetWidth, height: targetHeight },
      options.size
    );
    
    // 3. Canvas
    canvas.width = targetWidth;
    canvas.height = targetHeight;
    
    // 4. 
    if (options.backgroundColor) {
      ctx.fillStyle = options.backgroundColor;
      ctx.fillRect(0, 0, targetWidth, targetHeight);
    }
    
    // 5. 
    ctx.drawImage(
      image,
      scaleInfo.sourceX,
      scaleInfo.sourceY,
      scaleInfo.sourceWidth,
      scaleInfo.sourceHeight,
      scaleInfo.destX,
      scaleInfo.destY,
      scaleInfo.destWidth,
      scaleInfo.destHeight
    );
    
    // 6. 
    if (options.effects) {
      this.applyImageEffects(ctx, targetWidth, targetHeight, options.effects);
    }
    
    // 7. 
    const processedBlob = await this.canvasToBlob(canvas, 'image/jpeg', 0.9);
    
    return {
      canvas,
      blob: processedBlob,
      dimensions: { width: targetWidth, height: targetHeight }
    };
  }

  /**
   * 
   * @param file 
   * @returns HTMLImageElement
   */
  private static loadImage(file: File): Promise<HTMLImageElement> {
    return new Promise((resolve, reject) => {
      const img = new Image();
      const url = URL.createObjectURL(file);
      
      img.onload = () => {
        URL.revokeObjectURL(url);
        resolve(img);
      };
      
      img.onerror = () => {
        URL.revokeObjectURL(url);
        reject(new Error(''));
      };
      
      img.src = url;
    });
  }

  /**
   * 
   * @param imageSize 
   * @param targetSize 
   * @param sizeMode 
   * @returns 
   */
  private static calculateOptimalScale(
    imageSize: Size,
    targetSize: Size,
    sizeMode: 'cover' | 'contain' | 'stretch' | 'auto'
  ): ScaleInfo {
    switch (sizeMode) {
      case 'cover':
        // 
        const coverScale = Math.max(
          targetSize.width / imageSize.width,
          targetSize.height / imageSize.height
        );
        
        const scaledWidth = imageSize.width * coverScale;
        const scaledHeight = imageSize.height * coverScale;
        
        return {
          sourceX: 0,
          sourceY: 0,
          sourceWidth: imageSize.width,
          sourceHeight: imageSize.height,
          destX: (targetSize.width - scaledWidth) / 2,
          destY: (targetSize.height - scaledHeight) / 2,
          destWidth: scaledWidth,
          destHeight: scaledHeight
        };
        
      case 'contain':
        // 
        const containScale = Math.min(
          targetSize.width / imageSize.width,
          targetSize.height / imageSize.height
        );
        
        const containWidth = imageSize.width * containScale;
        const containHeight = imageSize.height * containScale;
        
        return {
          sourceX: 0,
          sourceY: 0,
          sourceWidth: imageSize.width,
          sourceHeight: imageSize.height,
          destX: (targetSize.width - containWidth) / 2,
          destY: (targetSize.height - containHeight) / 2,
          destWidth: containWidth,
          destHeight: containHeight
        };
        
      case 'stretch':
        // 
        return {
          sourceX: 0,
          sourceY: 0,
          sourceWidth: imageSize.width,
          sourceHeight: imageSize.height,
          destX: 0,
          destY: 0,
          destWidth: targetSize.width,
          destHeight: targetSize.height
        };
        
      default:
        // 
        return {
          sourceX: 0,
          sourceY: 0,
          sourceWidth: imageSize.width,
          sourceHeight: imageSize.height,
          destX: (targetSize.width - imageSize.width) / 2,
          destY: (targetSize.height - imageSize.height) / 2,
          destWidth: imageSize.width,
          destHeight: imageSize.height
        };
    }
  }

  /**
   * 
   * @param ctx Canvas
   * @param width 
   * @param height 
   * @param effects 
   */
  private static applyImageEffects(
    ctx: CanvasRenderingContext2D,
    width: number,
    height: number,
    effects: ImageEffects
  ): void {
    const filterString = [
      `blur(${effects.blur}px)`,
      `brightness(${effects.brightness})`,
      `contrast(${effects.contrast})`,
      `saturate(${effects.saturation})`,
      `sepia(${effects.sepia})`
    ].join(' ');
    
    ctx.filter = filterString;
    
    // 
    const imageData = ctx.getImageData(0, 0, width, height);
    ctx.clearRect(0, 0, width, height);
    ctx.putImageData(imageData, 0, 0);
    
    // 
    ctx.filter = 'none';
  }

  /**
   * CanvasBlob
   * @param canvas Canvas
   * @param type 
   * @param quality 
   * @returns Blob
   */
  private static canvasToBlob(
    canvas: HTMLCanvasElement, 
    type: string = 'image/jpeg', 
    quality: number = 0.9
  ): Promise<Blob> {
    return new Promise((resolve, reject) => {
      canvas.toBlob(
        (blob) => {
          if (blob) {
            resolve(blob);
          } else {
            reject(new Error('Canvas'));
          }
        },
        type,
        quality
      );
    });
  }
}

/**
 * CSS
 * @param effects 
 * @returns CSS
 */
export const buildFilterString = (effects: ImageEffects): string => {
  const filters = [];
  
  if (effects.blur > 0) {
    filters.push(`blur(${effects.blur}px)`);
  }
  
  if (effects.brightness !== 1) {
    filters.push(`brightness(${effects.brightness})`);
  }
  
  if (effects.contrast !== 1) {
    filters.push(`contrast(${effects.contrast})`);
  }
  
  if (effects.saturation !== 1) {
    filters.push(`saturate(${effects.saturation})`);
  }
  
  if (effects.sepia > 0) {
    filters.push(`sepia(${effects.sepia})`);
  }
  
  return filters.length > 0 ? filters.join(' ') : 'none';
};

/**
 * 
 * @returns 
 */
export const createDefaultImageEffects = (): ImageEffects => ({
  blur: 0,
  brightness: 1,
  contrast: 1,
  saturation: 1,
  sepia: 0
});

/**
 * 
 * @param effects 
 * @returns 
 */
export const isDefaultEffects = (effects: ImageEffects): boolean => {
  const defaultEffects = createDefaultImageEffects();
  return (
    effects.blur === defaultEffects.blur &&
    effects.brightness === defaultEffects.brightness &&
    effects.contrast === defaultEffects.contrast &&
    effects.saturation === defaultEffects.saturation &&
    effects.sepia === defaultEffects.sepia
  );
}; 