import { ValidationResult } from '../types/index';

// 10MB
const MAX_FILE_SIZE = 10 * 1024 * 1024;

// 
const SUPPORTED_FORMATS = [
  'image/jpeg',
  'image/png', 
  'image/webp',
  'image/svg+xml'
];

// 
const SUPPORTED_EXTENSIONS = [
  '.jpg',
  '.jpeg',
  '.png',
  '.webp',
  '.svg'
];

/**
 * 
 * @param file 
 * @returns 
 */
export const validateImage = async (file: File): Promise<ValidationResult> => {
  try {
    // 1. 
    if (file.size > MAX_FILE_SIZE) {
      return {
        isValid: false,
        error: `${Math.round(MAX_FILE_SIZE / 1024 / 1024)}MB`
      };
    }

    // 2. 
    if (!SUPPORTED_FORMATS.includes(file.type)) {
      return {
        isValid: false,
        error: ' JPEGPNGWebP  SVG '
      };
    }

    // 3. 
    const fileName = file.name.toLowerCase();
    const hasValidExtension = SUPPORTED_EXTENSIONS.some(ext => 
      fileName.endsWith(ext)
    );
    
    if (!hasValidExtension) {
      return {
        isValid: false,
        error: ''
      };
    }

    // 4. 
    const isValidImage = await checkImageIntegrity(file);
    if (!isValidImage) {
      return {
        isValid: false,
        error: ''
      };
    }

    return { isValid: true };
  } catch (error) {
    console.error(':', error);
    return {
      isValid: false,
      error: ''
    };
  }
};

/**
 * 
 * @param file 
 * @returns 
 */
export const checkImageIntegrity = async (file: File): Promise<boolean> => {
  return new Promise((resolve) => {
    const img = new Image();
    const url = URL.createObjectURL(file);
    
    img.onload = () => {
      URL.revokeObjectURL(url);
      // 
      resolve(img.width > 0 && img.height > 0);
    };
    
    img.onerror = () => {
      URL.revokeObjectURL(url);
      resolve(false);
    };
    
    img.src = url;
  });
};

/**
 * 
 * @param file 
 * @returns 
 */
export const getImageDimensions = async (file: File): Promise<{width: number, height: number}> => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    const url = URL.createObjectURL(file);
    
    img.onload = () => {
      URL.revokeObjectURL(url);
      resolve({
        width: img.width,
        height: img.height
      });
    };
    
    img.onerror = () => {
      URL.revokeObjectURL(url);
      reject(new Error(''));
    };
    
    img.src = url;
  });
};

/**
 * 
 * @param file 
 * @returns 
 */
export const isImageFile = (file: File): boolean => {
  return SUPPORTED_FORMATS.includes(file.type);
};

/**
 * 
 * @param bytes 
 * @returns 
 */
export const formatFileSize = (bytes: number): string => {
  if (bytes === 0) return '0 B';
  
  const k = 1024;
  const sizes = ['B', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

/**
 * 
 * @returns 
 */
export const getSupportedFormats = (): string[] => {
  return [...SUPPORTED_FORMATS];
};

/**
 * 
 * @returns 
 */
export const getSupportedExtensions = (): string[] => {
  return [...SUPPORTED_EXTENSIONS];
};

/**
 * 
 * @param file 
 * @returns 
 */
export const generateFileKey = (file: File): string => {
  return `${file.name}_${file.size}_${file.lastModified}`;
}; 