import React, { useState, useEffect } from 'react';
import { Grid, Box, useMediaQuery, useTheme } from '@mui/material';
import Layout from './components/Layout';
import MediaForm from './components/MediaForm';
import MediaCard from './components/MediaCard';
import { ThemeContextProvider } from './contexts/ThemeContext';
import { UserData } from './types';
import './App.css';

// WindowCompressionStream
declare global {
  interface Window {
    CompressionStream?: any;
  }
}

// 
function compressData(data: any): string {
  try {
    // 1. ISO
    const preparedData = JSON.stringify(data, (key, value) => {
      if (value instanceof Date) {
        return { __date: value.toISOString() };
      }
      // 2. null (undefinedJSONundefined)
      if (value === null) {
        return undefined;
      }
      return value;
    });
    
    // 3. 
    if (typeof window.CompressionStream === 'function') {
      // 
      // 
      return preparedData;
    }
    
    return preparedData;
  } catch (error) {
    console.error('', error);
    return JSON.stringify(data);
  }
}

// 
function decompressData(compressedData: string): any {
  try {
    // JSON
    return JSON.parse(compressedData, (key, value) => {
      // Date
      if (value && typeof value === 'object' && value.__date) {
        try {
          return new Date(value.__date);
        } catch (e) {
          console.error(":", e);
          return new Date(); // 
        }
      }
      return value;
    });
  } catch (error) {
    console.error('', error);
    return null;
  }
}

// 
class StorageManager {
  static KEY_PREFIX = 'mediaCard_';
  static MAIN_DATA_KEY = 'mediaCard_userData';
  
  // 
  static saveData(data: UserData): boolean {
    try {
      const compressedData = compressData(data);
      
      // 
      if (compressedData.length < 4000000) { // 
        localStorage.setItem(this.MAIN_DATA_KEY, compressedData);
        return true;
      } else {
        console.warn('');
        // 
        return false;
      }
    } catch (error) {
      console.error(':', error);
      
      // 
      if (error instanceof DOMException && 
         (error.name === 'QuotaExceededError' || error.name === 'NS_ERROR_DOM_QUOTA_REACHED')) {
        // 
        this.cleanupStorage();
        try {
          // 
          localStorage.setItem(this.MAIN_DATA_KEY, compressData(data));
          return true;
        } catch (innerError) {
          console.error(':', innerError);
          return false;
        }
      }
      return false;
    }
  }
  
  // 
  static loadData(): UserData | null {
    try {
      const compressedData = localStorage.getItem(this.MAIN_DATA_KEY);
      if (!compressedData) return null;
      
      return decompressData(compressedData);
    } catch (error) {
      console.error(':', error);
      return null;
    }
  }
  
  // 
  static cleanupStorage(): void {
    // 
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith(this.KEY_PREFIX) && key !== this.MAIN_DATA_KEY) {
        // mediaCard
        localStorage.removeItem(key);
      }
    }
    
    // 
    if (localStorage.length > 10) { // 10
      const keysToKeep = [this.MAIN_DATA_KEY];
      const keysToRemove = [];
      
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key && !keysToKeep.includes(key)) {
          keysToRemove.push(key);
        }
      }
      
      // 
      keysToRemove.forEach(key => localStorage.removeItem(key));
    }
  }
}

const initialUserData: UserData = {
  socialMedia: {
    // 
    wechat: null,
    douyin: null,
    xiaohongshu: null,
    weibo: null,
    bilibili: null,
    videoAccount: null,
    kuaishou: null,
    zhihu: null,
    juejin: null,
    
    // 
    twitter: null,
    tiktok: null,
    facebook: null,
    instagram: null,
    youtube: null,
    linkedin: null,
    reddit: null,
    discord: null,
    telegram: null,
    snapchat: null
  },
  contactInfo: {
    wechatId: '',
    email: ''
  },
  updatedAt: new Date()
};

const App: React.FC = () => {
  const [userData, setUserData] = useState<UserData>(initialUserData);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  // 
  useEffect(() => {
    const loadedData = StorageManager.loadData();
    if (loadedData) {
      setUserData(loadedData);
    }
  }, []);

  const handleUpdateUserData = (data: UserData) => {
    setUserData(data);
    // 
    StorageManager.saveData(data);
  };

  return (
    <div className="App" style={{ 
      background: 'linear-gradient(135deg, #1a2a6c, #b21f1f, #fdbb2d)',
      backgroundSize: 'cover',
      backgroundAttachment: 'fixed',
      minHeight: '100vh',
      padding: isMobile ? '10px 0' : '20px 0', // 
      position: 'relative',
      overflowX: 'hidden', // 
      width: '100%' //  100%
    }}>
      <div style={{
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundImage: 'url(/mediacardfluence2/bg-pattern.png)',
        backgroundSize: '200px',
        opacity: 0.05,
        zIndex: 0,
        pointerEvents: 'none'
      }} />
      <ThemeContextProvider>
        <Layout>
          <Grid 
            container 
            spacing={{ xs: 2, sm: 4, md: 6 }} // 
            direction="column"
            sx={{ mb: 4, width: '100%', mx: 0 }} // 
          >
            <Grid item xs={12}>
              <MediaForm 
                userData={userData} 
                onUpdateUserData={handleUpdateUserData} 
              />
            </Grid>
            <Grid 
              item 
              xs={12}
              sx={{
                mt: { xs: 1, sm: 2, md: 4 }, // 
                display: 'flex',
                justifyContent: 'center',
                width: '100%' //  100%
              }}
            >
              <Box 
                sx={{ 
                  width: '100%',
                  overflow: 'visible',
                  position: 'relative',
                  px: { xs: 1, sm: 2 } // 
                }}
              >
                <MediaCard userData={userData} />
              </Box>
            </Grid>
          </Grid>
        </Layout>
      </ThemeContextProvider>
    </div>
  );
};

export default App;
