import React, { useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardMedia,
  CardContent,
  Grid,
  ToggleButtonGroup,
  ToggleButton,
  Chip
} from '@mui/material';
import { ThemeBackground, ThemeImage } from '../../types/index';
import { themeBackgrounds } from '../../data/themeBackgrounds';
import { useThemeContext } from '../../contexts/ThemeContext';

interface ThemeSelectorProps {
  onThemeSelect: (themeImage: ThemeImage) => void;
  selectedThemeId?: string | null;
  selectedVariantId?: string | null;
  disabled?: boolean;
  onFontColorSuggestion?: (textColor: string) => void; // 
  cardWidth?: number; // 
  cardHeight?: number; // 
}

const ThemeSelector: React.FC<ThemeSelectorProps> = ({
  onThemeSelect,
  selectedThemeId,
  selectedVariantId,
  disabled = false,
  onFontColorSuggestion,
  cardWidth = 400,
  cardHeight = 600
}) => {
  const { t } = useThemeContext();
  const [selectedTheme, setSelectedTheme] = useState<string | null>(selectedThemeId || null);

  const handleThemeClick = (themeBackground: ThemeBackground) => {
    if (disabled) return;
    
    setSelectedTheme(themeBackground.id);
    
    // 
    if (themeBackground.variants.length > 0) {
      const firstVariant = themeBackground.variants[0];
      
      // 
      onThemeSelect({
        themeId: themeBackground.id,
        variantId: firstVariant.id,
        path: firstVariant.path
      });
      
      // BackgroundImagePanel
      // if (onFontColorSuggestion) {
      //   onFontColorSuggestion(firstVariant.textColor);
      // }
    }
  };

  const handleVariantChange = (
    _event: React.MouseEvent<HTMLElement>,
    newVariantId: string
  ) => {
    if (!newVariantId || !selectedTheme || disabled) return;

    const currentTheme = themeBackgrounds.find(t => t.id === selectedTheme);
    if (currentTheme) {
      const variant = currentTheme.variants.find(v => v.id === newVariantId);
      if (variant) {
        // 
        onThemeSelect({
          themeId: selectedTheme,
          variantId: variant.id,
          path: variant.path
        });
        
        // BackgroundImagePanel
        // if (onFontColorSuggestion) {
        //   onFontColorSuggestion(variant.textColor);
        // }
      }
    }
  };

  const getSelectedTheme = (): ThemeBackground | null => {
    return themeBackgrounds.find(t => t.id === selectedTheme) || null;
  };

  return (
    <Box>
      <Typography variant="body2" gutterBottom sx={{ mb: 2 }}>
        {t('selectThemeBackground') || ''}
      </Typography>
      
      {/*  */}
      <Grid container spacing={2} sx={{ mb: 3 }}>
        {themeBackgrounds.map((themeBackground) => (
          <Grid item xs={6} sm={4} key={themeBackground.id}>
            <Card
              sx={{
                cursor: disabled ? 'not-allowed' : 'pointer',
                border: selectedTheme === themeBackground.id ? 2 : 1,
                borderColor: selectedTheme === themeBackground.id 
                  ? 'primary.main' 
                  : 'divider',
                transition: 'all 0.2s ease',
                opacity: disabled ? 0.6 : 1,
                '&:hover': {
                  borderColor: disabled ? 'divider' : 'primary.light',
                  transform: disabled ? 'none' : 'translateY(-2px)',
                  boxShadow: disabled ? 1 : 4
                }
              }}
              onClick={() => handleThemeClick(themeBackground)}
            >
              <CardMedia
                component="div"
                sx={{
                  height: 80,
                  background: 'linear-gradient(45deg, #f5f5f5 0%, #e0e0e0 100%)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  position: 'relative',
                  overflow: 'hidden'
                }}
              >
                {/*  */}
                <Box
                  sx={{
                    width: '100%',
                    height: '100%',
                    background: themeBackground.variants.length > 0 
                      ? `url(${themeBackground.variants[0].path})` 
                      : 'linear-gradient(45deg, #f5f5f5 0%, #e0e0e0 100%)',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat'
                  }}
                />
                {selectedTheme === themeBackground.id && (
                  <Chip
                    label={t('selected') || ''}
                    size="small"
                    color="primary"
                    sx={{
                      position: 'absolute',
                      top: 4,
                      right: 4
                    }}
                  />
                )}
              </CardMedia>
              <CardContent sx={{ p: 1.5, '&:last-child': { pb: 1.5 } }}>
                <Typography variant="caption" fontWeight="medium">
                  {t(`theme.${themeBackground.id}`) || themeBackground.name}
                </Typography>
                <Typography 
                  variant="caption" 
                  color="text.secondary" 
                  display="block"
                  sx={{ fontSize: '0.65rem' }}
                >
                  {t(`theme.${themeBackground.id}.description`) || themeBackground.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/*  */}
      {selectedTheme && getSelectedTheme() && (
        <Box>
          <Typography variant="body2" gutterBottom sx={{ mb: 1 }}>
            {t('selectStyleVariant') || ''}
          </Typography>
          <ToggleButtonGroup
            value={selectedVariantId}
            exclusive
            onChange={handleVariantChange}
            size="small"
            disabled={disabled}
            sx={{ 
              flexWrap: 'wrap',
              '& .MuiToggleButton-root': {
                mb: 1,
                mr: 1
              }
            }}
          >
            {getSelectedTheme()!.variants.map((variant) => (
              <ToggleButton 
                key={variant.id} 
                value={variant.id}
                sx={{
                  textTransform: 'none',
                  fontSize: '0.75rem',
                  px: 2,
                  py: 0.5
                }}
              >
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <Box
                    sx={{
                      width: 12,
                      height: 12,
                      borderRadius: '50%',
                      backgroundColor: variant.primaryColor,
                      mr: 1,
                      border: '1px solid',
                      borderColor: 'divider'
                    }}
                  />
                  {t(`variant.${variant.id}`) || variant.name}
                </Box>
              </ToggleButton>
            ))}
          </ToggleButtonGroup>

          {/*  */}
          {selectedVariantId && (
            <Box sx={{ mt: 2, p: 1.5, backgroundColor: 'action.hover', borderRadius: 1 }}>
              <Typography variant="caption" color="text.secondary">
                {(() => {
                  const variant = getSelectedTheme()?.variants.find(v => v.id === selectedVariantId);
                  return variant?.darkMode 
                    ? (t('darkTheme') || '')
                    : (t('lightTheme') || '');
                })()}
              </Typography>
            </Box>
          )}
        </Box>
      )}
    </Box>
  );
};

export default ThemeSelector; 