import React, { useState, useEffect } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Container,
  Box,
  IconButton,
  useMediaQuery,
  Tooltip,
  Drawer,
  List,
  ListItem,
  CssBaseline
} from '@mui/material';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import GitHubIcon from '@mui/icons-material/GitHub';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import TranslateIcon from '@mui/icons-material/Translate';
import { useThemeContext } from '../contexts/ThemeContext';
import { createFavicon } from '../utils/favicon';

interface LayoutProps {
  children?: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = ({ children }) => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const isMobile = useMediaQuery('(max-width:600px)');
  const { mode, toggleTheme, language, setLanguage, t } = useThemeContext();
  const isDarkMode = mode === 'dark';

  // favicon
  useEffect(() => {
    createFavicon(isDarkMode);
  }, [isDarkMode]);

  const handleDrawerToggle = () => {
    setDrawerOpen(!drawerOpen);
  };

  return (
    <>
      <CssBaseline />
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
        <AppBar 
          position="static" 
          color="transparent"
          elevation={0}
          sx={{ 
            backgroundColor: isDarkMode ? 'rgba(18, 18, 18, 0.95)' : 'rgba(255, 255, 255, 0.95)',
            backdropFilter: 'blur(10px)',
            borderBottom: '1px solid',
            borderColor: isDarkMode ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)'
          }}
        >
          <Container maxWidth="lg" sx={{ px: { xs: 1, sm: 2, md: 3 } }}>
            <Toolbar 
              disableGutters 
              sx={{ 
                display: 'flex', 
                justifyContent: 'space-between' 
              }}
            >
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Typography 
                  variant="h6" 
                  component="div" 
                  sx={{ 
                    fontWeight: 700,
                    backgroundImage: isDarkMode
                      ? 'linear-gradient(90deg, #90caf9, #ce93d8)'
                      : 'linear-gradient(90deg, #1976d2, #9c27b0)',
                    backgroundClip: 'text',
                    color: 'transparent',
                    WebkitBackgroundClip: 'text',
                    textShadow: isDarkMode
                      ? '0 0 30px rgba(144, 202, 249, 0.3)'
                      : '0 0 30px rgba(25, 118, 210, 0.3)',
                  }}
                >
                  {t('appTitle')}
                </Typography>
              </Box>

              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                {isMobile ? (
                  <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="end"
                    onClick={handleDrawerToggle}
                    sx={{ 
                      color: isDarkMode ? '#f5f5f5' : '#555555' 
                    }}
                  >
                    <MenuIcon />
                  </IconButton>
                ) : (
                  <>
                    <Tooltip title={language === 'zh' ? 'Switch to English' : ''}>
                      <IconButton 
                        onClick={() => setLanguage(language === 'zh' ? 'en' : 'zh')} 
                        sx={{ 
                          color: isDarkMode ? '#f5f5f5' : '#555555',
                          transition: 'transform 0.3s ease, color 0.3s ease',
                          '&:hover': {
                            transform: 'scale(1.1)',
                            color: isDarkMode ? '#90caf9' : '#1976d2'
                          }
                        }}
                      >
                        <TranslateIcon />
                      </IconButton>
                    </Tooltip>
                    
                    <Tooltip title={isDarkMode ? t('toggleLightMode') : t('toggleDarkMode')}>
                      <IconButton 
                        onClick={toggleTheme} 
                        sx={{ 
                          color: isDarkMode ? '#f5f5f5' : '#555555',
                          transition: 'transform 0.3s ease, color 0.3s ease',
                          ml: 1,
                          '&:hover': {
                            transform: 'rotate(30deg)',
                            color: isDarkMode ? '#90caf9' : '#1976d2'
                          }
                        }}
                      >
                        {isDarkMode ? <Brightness7Icon /> : <Brightness4Icon />}
                      </IconButton>
                    </Tooltip>
                    
                    <Tooltip title={t('githubRepo')}>
                      <IconButton 
                        component="a" 
                        href="https://github.com/maigamo/mediaCardfluence2" 
                        target="_blank"
                        sx={{ 
                          color: isDarkMode ? '#f5f5f5' : '#555555',
                          ml: 1,
                          transition: 'transform 0.3s ease, color 0.3s ease',
                          '&:hover': {
                            transform: 'scale(1.1)',
                            color: isDarkMode ? '#90caf9' : '#1976d2'
                          }
                        }}
                      >
                        <GitHubIcon />
                      </IconButton>
                    </Tooltip>
                  </>
                )}
              </Box>
            </Toolbar>
          </Container>
        </AppBar>

        {/*  */}
        <Drawer
          anchor="right"
          open={drawerOpen}
          onClose={handleDrawerToggle}
          sx={{
            '& .MuiDrawer-paper': {
              width: '100%',
              maxWidth: '300px',
              background: isDarkMode 
                ? 'rgba(18, 18, 18, 0.95)' 
                : 'rgba(255, 255, 255, 0.95)',
              backdropFilter: 'blur(15px)',
              boxShadow: '0 0 25px rgba(0,0,0,0.3)',
              borderLeft: '1px solid',
              borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)'
            }
          }}
        >
          <Box sx={{ 
            p: 2.5, 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            borderBottom: '1px solid',
            borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)'
          }}>
            <Typography 
              variant="h6" 
              sx={{ 
                fontWeight: 700,
                background: isDarkMode 
                  ? 'linear-gradient(45deg, #90caf9, #64b5f6, #42a5f5)' 
                  : 'linear-gradient(45deg, #1976d2, #2196f3, #42a5f5)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
                backgroundClip: 'text',
                textFillColor: 'transparent',
                letterSpacing: '0.5px'
              }}
            >
              {t('appTitle')}
            </Typography>
            <IconButton 
              onClick={handleDrawerToggle}
              sx={{
                color: isDarkMode ? '#f5f5f5' : '#555555',
                '&:hover': {
                  backgroundColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
                  transform: 'rotate(90deg)',
                  transition: 'transform 0.3s ease'
                }
              }}
            >
              <CloseIcon />
            </IconButton>
          </Box>
          <List sx={{ p: 2.5 }}>
            <ListItem sx={{ mb: 2, p: 0 }}>
              <Box
                onClick={() => setLanguage(language === 'zh' ? 'en' : 'zh')}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  width: '100%',
                  py: 1.8,
                  px: 2.5,
                  borderRadius: '16px',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  border: '1px solid',
                  borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                  bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                  backdropFilter: 'blur(10px)',
                  boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                  '&:hover': {
                    backgroundColor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.03)',
                    transform: 'translateY(-3px)',
                    boxShadow: '0 8px 16px rgba(0,0,0,0.12)',
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.12)'
                  }
                }}
              >
                <Box sx={{ 
                  mr: 2.5,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  width: 42,
                  height: 42,
                  borderRadius: '12px',
                  background: isDarkMode 
                    ? 'linear-gradient(135deg, #2196f3, #21cbf3)' 
                    : 'linear-gradient(135deg, #1976d2, #42a5f5)',
                  boxShadow: '0 4px 10px rgba(33, 150, 243, 0.3)'
                }}>
                  <TranslateIcon sx={{ color: '#fff', fontSize: '1.4rem' }} />
                </Box>
                <Box>
                  <Typography sx={{ fontWeight: 600, fontSize: '1rem', mb: 0.3 }}>
                    {language === 'zh' ? 'Switch to English' : ''}
                  </Typography>
                  <Typography variant="body2" sx={{ 
                    color: isDarkMode ? 'rgba(255,255,255,0.6)' : 'rgba(0,0,0,0.6)',
                    fontSize: '0.8rem'
                  }}>
                    {language === 'zh' ? 'Change language to English' : ''}
                  </Typography>
                </Box>
              </Box>
            </ListItem>
            <ListItem sx={{ mb: 2, p: 0 }}>
              <Box
                onClick={toggleTheme}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  width: '100%',
                  py: 1.8,
                  px: 2.5,
                  borderRadius: '16px',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  border: '1px solid',
                  borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                  bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                  backdropFilter: 'blur(10px)',
                  boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                  '&:hover': {
                    backgroundColor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.03)',
                    transform: 'translateY(-3px)',
                    boxShadow: '0 8px 16px rgba(0,0,0,0.12)',
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.12)'
                  }
                }}
              >
                <Box sx={{ 
                  mr: 2.5,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  width: 42,
                  height: 42,
                  borderRadius: '12px',
                  background: isDarkMode 
                    ? 'linear-gradient(135deg, #f9a825, #fdd835)' 
                    : 'linear-gradient(135deg, #546e7a, #78909c)',
                  boxShadow: isDarkMode 
                    ? '0 4px 10px rgba(249, 168, 37, 0.3)' 
                    : '0 4px 10px rgba(84, 110, 122, 0.3)'
                }}>
                  {isDarkMode ? 
                    <Brightness7Icon sx={{ color: '#fff', fontSize: '1.4rem' }} /> : 
                    <Brightness4Icon sx={{ color: '#fff', fontSize: '1.4rem' }} />
                  }
                </Box>
                <Box>
                  <Typography sx={{ fontWeight: 600, fontSize: '1rem', mb: 0.3 }}>
                    {isDarkMode ? t('toggleLightMode') : t('toggleDarkMode')}
                  </Typography>
                  <Typography variant="body2" sx={{ 
                    color: isDarkMode ? 'rgba(255,255,255,0.6)' : 'rgba(0,0,0,0.6)',
                    fontSize: '0.8rem'
                  }}>
                    {isDarkMode ? '' : ''}
                  </Typography>
                </Box>
              </Box>
            </ListItem>
            <ListItem sx={{ p: 0 }}>
              <Box
                component="a"
                href="https://github.com/maigamo/mediaCardfluence2"
                target="_blank"
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  textDecoration: 'none',
                  color: 'inherit',
                  width: '100%',
                  py: 1.8,
                  px: 2.5,
                  borderRadius: '16px',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  border: '1px solid',
                  borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                  bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                  backdropFilter: 'blur(10px)',
                  boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                  '&:hover': {
                    backgroundColor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.03)',
                    transform: 'translateY(-3px)',
                    boxShadow: '0 8px 16px rgba(0,0,0,0.12)',
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.12)'
                  }
                }}
              >
                <Box sx={{ 
                  mr: 2.5,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  width: 42,
                  height: 42,
                  borderRadius: '12px',
                  background: isDarkMode 
                    ? 'linear-gradient(135deg, #333333, #555555)' 
                    : 'linear-gradient(135deg, #333333, #555555)',
                  boxShadow: '0 4px 10px rgba(0, 0, 0, 0.25)'
                }}>
                  <GitHubIcon sx={{ color: '#fff', fontSize: '1.4rem' }} />
                </Box>
                <Box>
                  <Typography sx={{ fontWeight: 600, fontSize: '1rem', mb: 0.3 }}>
                    {t('githubRepo')}
                  </Typography>
                  <Typography variant="body2" sx={{ 
                    color: isDarkMode ? 'rgba(255,255,255,0.6)' : 'rgba(0,0,0,0.6)',
                    fontSize: '0.8rem'
                  }}>
                    {t('viewSourceCode')}
                  </Typography>
                </Box>
              </Box>
            </ListItem>
          </List>
          <Box sx={{ 
            p: 2.5, 
            mt: 'auto', 
            borderTop: '1px solid', 
            borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: 1
          }}>
            <Typography variant="caption" sx={{ 
              color: isDarkMode ? 'rgba(255,255,255,0.5)' : 'rgba(0,0,0,0.5)',
              fontSize: '0.7rem',
              letterSpacing: '0.5px'
            }}>
               2023 MediaCardFluence
            </Typography>
          </Box>
        </Drawer>

        <Container 
          maxWidth="lg" 
          sx={{ 
            flexGrow: 1, 
            py: 4,
            px: { xs: 2, sm: 3, md: 4 },
            display: 'flex',
            flexDirection: 'column',
            transition: 'background-color 0.5s ease'
          }}
        >
          <Box 
            sx={{ 
              display: 'flex',
              flexDirection: 'column',
              flexGrow: 1,
              width: '100%',
              maxWidth: '100%'
            }}
          >
            {children}
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default Layout; 