import React, { useCallback } from 'react';
import {
  Box,
  Typography,
  Slider,
  Select,
  MenuItem,
  TextField,
  InputLabel,
  FormControl,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Grid,
  ToggleButtonGroup,
  ToggleButton,
  Switch,
  FormControlLabel
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { CardConfig, ExportOptions, AspectRatio, BackgroundImageConfig } from '../types/index';
import { useThemeContext } from '../contexts/ThemeContext';
import BackgroundImagePanel from './backgroundImage/BackgroundImagePanel';

interface CardStyleConfigProps {
  cardConfig: CardConfig;
  exportOptions: ExportOptions;
  onConfigChange: (config: CardConfig) => void;
  onExportOptionsChange: (options: ExportOptions) => void;
}

const CardStyleConfig: React.FC<CardStyleConfigProps> = ({
  cardConfig,
  exportOptions,
  onConfigChange,
  onExportOptionsChange
}) => {
  const { t } = useThemeContext();

  const handleChange = (property: keyof CardConfig, value: any) => {
    onConfigChange({
      ...cardConfig,
      [property]: value
    });
  };

  const handleWatermarkChange = (property: keyof CardConfig['watermark'], value: any) => {
    onConfigChange({
      ...cardConfig,
      watermark: {
        ...cardConfig.watermark,
        [property]: value
      }
    });
  };

  const handleExportOptionChange = (property: keyof ExportOptions, value: any) => {
    onExportOptionsChange({
      ...exportOptions,
      [property]: value
    });
  };

  // 
  const handleBackgroundImageChange = useCallback((backgroundImage: BackgroundImageConfig) => {
    onConfigChange({
      ...cardConfig,
      backgroundImage
    });
  }, [onConfigChange, cardConfig]);

  // 
  const handleSuggestedTextColor = useCallback((suggestedTextColor: string) => {
    // 
    onConfigChange({
      ...cardConfig,
      textColor: suggestedTextColor
    });
  }, [onConfigChange, cardConfig]);

  // 
  const handleAspectRatioChange = (
    _event: React.MouseEvent<HTMLElement>,
    newAspectRatio: AspectRatio,
  ) => {
    if (newAspectRatio !== null) {
      handleExportOptionChange('aspectRatio', newAspectRatio);
    }
  };

  const fontFamilies = [
    'Inter, system-ui, sans-serif',
    'Arial, sans-serif',
    'Helvetica, sans-serif',
    'Georgia, serif',
    'Tahoma, sans-serif',
    'Verdana, sans-serif',
    'Times New Roman, serif',
    'Courier New, monospace'
  ];

  const watermarkPositions = [
    { value: 'bottomRight', label: t('bottomRight') || '' },
    { value: 'bottomLeft', label: t('bottomLeft') || '' },
    { value: 'topRight', label: t('topRight') || '' },
    { value: 'topLeft', label: t('topLeft') || '' },
    { value: 'center', label: t('center') || '' }
  ];

  return (
    <Box>
      <Accordion sx={{ mb: 2 }} defaultExpanded={false}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="card-style-config-content"
          id="card-style-config-header"
        >
          <Typography variant="subtitle1">{t('cardSettings')}</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography gutterBottom>{t('cardTitle')}</Typography>
            <TextField
              fullWidth
              size="small"
              value={cardConfig.cardTitle}
              onChange={(e) => handleChange('cardTitle', e.target.value)}
              placeholder={t('cardTitle')}
            />
          </Grid>
          
          <Grid item xs={12}>
            <Typography gutterBottom>{t('aspectRatio') || ''}</Typography>
            <ToggleButtonGroup
              color="primary"
              value={exportOptions.aspectRatio}
              exclusive
              onChange={handleAspectRatioChange}
              aria-label="aspectRatio"
              size="small"
              fullWidth
            >
              <ToggleButton value="16:9">16:9</ToggleButton>
              <ToggleButton value="4:3">4:3</ToggleButton>
              <ToggleButton value="1:1">1:1</ToggleButton>
              <ToggleButton value="custom">{t('custom') || ''}</ToggleButton>
            </ToggleButtonGroup>
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <Typography gutterBottom>{t('cardSize') || ''}</Typography>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <TextField
                label={t('width') || ''}
                type="number"
                size="small"
                value={cardConfig.width}
                onChange={(e) => handleChange('width', Number(e.target.value))}
                inputProps={{ min: 400, max: 1200 }}
              />
              <TextField
                label={t('height') || ''}
                type="number"
                size="small"
                value={cardConfig.height}
                onChange={(e) => handleChange('height', Number(e.target.value))}
                inputProps={{ min: 225, max: 675 }}
              />
            </Box>
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <Typography gutterBottom>{t('borderRadius') || ''}</Typography>
            <Slider
              value={cardConfig.borderRadius}
              onChange={(_, value) => handleChange('borderRadius', value)}
              step={1}
              marks
              min={0}
              max={20}
              valueLabelDisplay="auto"
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <Typography gutterBottom>{t('iconSize') || ''}</Typography>
            <Slider
              value={cardConfig.iconSize}
              onChange={(_, value) => handleChange('iconSize', value)}
              step={2}
              marks
              min={16}
              max={40}
              valueLabelDisplay="auto"
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <FormControl fullWidth size="small">
              <InputLabel id="font-family-label">{t('font') || ''}</InputLabel>
              <Select
                labelId="font-family-label"
                id="font-family-select"
                value={cardConfig.fontFamily}
                label={t('font') || ''}
                onChange={(e) => handleChange('fontFamily', e.target.value)}
              >
                {fontFamilies.map((font) => (
                  <MenuItem key={font} value={font} style={{ fontFamily: font }}>
                    {font.split(',')[0]}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <TextField
              label={t('backgroundColor') || ''}
              type="color"
              size="small"
              value={cardConfig.backgroundColor}
              onChange={(e) => handleChange('backgroundColor', e.target.value)}
              sx={{ width: '100%' }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <TextField
              label={t('textColor') || ''}
              type="color"
              size="small"
              value={cardConfig.textColor}
              onChange={(e) => handleChange('textColor', e.target.value)}
              sx={{ width: '100%' }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>

          <Grid item xs={12}>
            <Typography variant="subtitle1" sx={{ mt: 2, mb: 1 }}>{t('watermarkSettings') || ''}</Typography>
            <FormControlLabel 
              control={
                <Switch 
                  checked={cardConfig.watermark.enabled}
                  onChange={(e) => handleWatermarkChange('enabled', e.target.checked)}
                />
              } 
              label={t('showWatermark') || ''} 
            />
          </Grid>

          {cardConfig.watermark.enabled && (
            <>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label={t('watermarkText') || ''}
                  size="small"
                  value={cardConfig.watermark.text}
                  onChange={(e) => handleWatermarkChange('text', e.target.value)}
                />
              </Grid>
              
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth size="small">
                  <InputLabel>{t('watermarkPosition') || ''}</InputLabel>
                  <Select
                    value={cardConfig.watermark.position}
                    label={t('watermarkPosition') || ''}
                    onChange={(e) => handleWatermarkChange('position', e.target.value)}
                  >
                    {watermarkPositions.map((pos) => (
                      <MenuItem key={pos.value} value={pos.value}>
                        {pos.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              
              <Grid item xs={12} sm={6}>
                <Typography gutterBottom>{t('opacity') || ''}</Typography>
                <Slider
                  value={cardConfig.watermark.opacity}
                  onChange={(_, value) => handleWatermarkChange('opacity', value)}
                  step={0.1}
                  min={0.1}
                  max={1}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${Math.round(value * 100)}%`}
                />
              </Grid>
              
              <Grid item xs={12} sm={6}>
                <Typography gutterBottom>{t('fontSize') || ''}</Typography>
                <Slider
                  value={cardConfig.watermark.fontSize}
                  onChange={(_, value) => handleWatermarkChange('fontSize', value)}
                  step={1}
                  min={10}
                  max={36}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${value}px`}
                />
              </Grid>
              
              <Grid item xs={12} sm={6}>
                <TextField
                  label={t('watermarkColor') || ''}
                  type="color"
                  size="small"
                  value={cardConfig.watermark.color}
                  onChange={(e) => handleWatermarkChange('color', e.target.value)}
                  sx={{ width: '100%' }}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
            </>
          )}

          <Grid item xs={12}>
            <Typography variant="subtitle1" sx={{ mt: 2, mb: 1 }}>{t('exportSettings') || ''}</Typography>
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <FormControl fullWidth size="small">
              <InputLabel id="export-format-label">{t('exportFormat') || ''}</InputLabel>
              <Select
                labelId="export-format-label"
                id="export-format-select"
                value={exportOptions.format}
                label={t('exportFormat') || ''}
                onChange={(e) => handleExportOptionChange('format', e.target.value)}
              >
                <MenuItem value="png">{t('pngFormat') || 'PNG ()'}</MenuItem>
                <MenuItem value="jpeg">{t('jpegFormat') || 'JPEG ()'}</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <Typography gutterBottom>{t('scale') || ''}</Typography>
            <Slider
              value={exportOptions.scale}
              onChange={(_, value) => handleExportOptionChange('scale', value)}
              step={0.5}
              marks={[
                { value: 1, label: '1x' },
                { value: 2, label: '2x' },
                { value: 3, label: '3x' },
              ]}
              min={1}
              max={3}
              valueLabelDisplay="auto"
              valueLabelFormat={(value) => `${value}x`}
            />
          </Grid>
          
          {exportOptions.format === 'jpeg' && (
            <Grid item xs={12}>
              <Typography gutterBottom>{t('quality') || ''}</Typography>
              <Box sx={{ px: 1 }}>
                <Slider
                  value={exportOptions.quality}
                  onChange={(_, value) => handleExportOptionChange('quality', value)}
                  step={5}
                  marks={[
                    { value: 60, label: t('low') || '' },
                    { value: 80, label: t('medium') || '' },
                    { value: 100, label: t('high') || '' },
                  ]}
                  min={60}
                  max={100}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${value}%`}
                />
              </Box>
            </Grid>
          )}
        </Grid>
      </AccordionDetails>
    </Accordion>

    {/*  */}
    <Accordion sx={{ mb: 2 }} defaultExpanded={false}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="background-image-config-content"
        id="background-image-config-header"
      >
        <Typography variant="subtitle1">{t('backgroundImage') || ''}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <BackgroundImagePanel
          config={cardConfig.backgroundImage}
          onChange={handleBackgroundImageChange}
          onFontColorSuggestion={handleSuggestedTextColor}
          cardWidth={cardConfig.width}
          cardHeight={typeof cardConfig.height === 'number' ? cardConfig.height : parseInt(String(cardConfig.height)) || 600}
        />
      </AccordionDetails>
    </Accordion>
    </Box>
  );
};

export default CardStyleConfig; 