/**
 * 
 */
export const formatDate = (date: Date | string | any): string => {
  try {
    const d = new Date(date);
    if (isNaN(d.getTime())) {
      return new Date().toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      }).replace(/\//g, '-');
    }
    return d.toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    }).replace(/\//g, '-');
  } catch (error) {
    return new Date().toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    }).replace(/\//g, '-');
  }
};

/**
 * 
 */
export const getResponsiveSize = (baseSize: number, isMobile: boolean, isTablet: boolean): number => {
  if (isMobile) return baseSize * 0.7;
  if (isTablet) return baseSize * 0.85;
  return baseSize;
};

/**
 *  - 
 */
export const getLayoutConfig = (cardConfig: any, isMobile: boolean, isTablet: boolean) => {
  // 800x450
  const baseWidth = 800;
  
  // 
  const ratio = cardConfig.width / baseWidth;
  
  // 
  return {
    // 
    titleFontSize: `${Math.max(1.5, 2.125 * ratio)}rem`,
    titleMarginBottom: `${Math.max(8, 16 * ratio)}px`,
    
    // 
    chipHeight: `${Math.max(24, 32 * ratio)}px`,
    chipFontSize: `${Math.max(0.65, 0.875 * ratio)}rem`,
    chipPadding: `${Math.max(4, 8 * ratio)}px ${Math.max(6, 12 * ratio)}px`,
    chipMarginBottom: `${Math.max(8, 20 * ratio)}px`,
    
    // 
    contactLabelFontSize: `${Math.max(0.7, 0.95 * ratio)}rem`,
    
    // 
    gridSpacing: Math.max(1, Math.round(3 * ratio)),
    
    // 
    platformItemPadding: `${Math.max(6, 12 * ratio)}px`,
    platformItemBorderRadius: `${Math.max(6, 12 * ratio)}px`,
    platformItemMarginBottom: `${Math.max(4, 8 * ratio)}px`,
    
    // 
    iconSize: getResponsiveSize(cardConfig.iconSize, isMobile, isTablet),
    iconMarginRight: `${Math.max(4, 8 * ratio)}px`,
    iconPadding: `${Math.max(1, 3 * ratio)}px`,
    
    // 
    platformNameFontSize: `${Math.max(0.6, 0.7 * ratio)}rem`,
    
    // 
    followersFontSize: `${Math.max(0.75, 0.9 * ratio)}rem`,
    
    // 
    statsBoxBottom: `${Math.max(10, 20 * ratio)}px`,
    statsBoxRight: `${Math.max(10, 20 * ratio)}px`,
    statsBoxPadding: `${Math.max(5, 10 * ratio)}px ${Math.max(10, 15 * ratio)}px`,
    statsBoxBorderRadius: `${Math.max(4, 8 * ratio)}px`,
    statsTotalFontSize: `${Math.max(0.75, 0.875 * ratio)}rem`,
    statsDateFontSize: `${Math.max(0.6, 0.75 * ratio)}rem`,

    // 
    cardPadding: `${Math.max(16, 32 * ratio)}px`,
  };
};

/**
 * 
 */
export const getDimensions = (cardConfig: any, cardHeight: number | null) => {
  const isFixedHeight = typeof cardConfig.height === 'number';
  
  return {
    width: cardConfig.width,
    height: isFixedHeight ? cardConfig.height : (cardHeight || 'auto'),
    isFixedHeight
  };
};

/**
 * 
 */
export const formatNumber = (num: number | null | undefined, language: string = 'zh'): string => {
  if (num === null || num === undefined) return '0';
  
  if (language === 'zh') {
    // 
    if (num >= 100000000) { // 
      const formatted = (num / 100000000).toFixed(1);
      // 0
      return `${formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted}`;
    } else if (num >= 10000) { // 
      const formatted = (num / 10000).toFixed(1);
      // 0
      return `${formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted}`;
    } else if (num >= 1000) { // 
      return `${Math.floor(num / 1000)}`;
    }
  } else {
    // 
    if (num >= 1000000000) { // Billion
      const formatted = (num / 1000000000).toFixed(1);
      return `${formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted}B`;
    } else if (num >= 1000000) { // Million
      const formatted = (num / 1000000).toFixed(1);
      return `${formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted}M`;
    } else if (num >= 1000) { // Thousand
      const formatted = (num / 1000).toFixed(1);
      return `${formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted}K`;
    }
  }
  
  return num.toString();
}; 