import React, { useState, useEffect, useMemo } from 'react';
import {
  Box,
  Typography,
  Switch,
  FormControlLabel,
  ToggleButtonGroup,
  ToggleButton,
  Slider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Divider
} from '@mui/material';
import {
  Upload as UploadIcon,
  Palette as PaletteIcon
} from '@mui/icons-material';
import { BackgroundImageConfig, UploadedImage, ThemeImage, ImageEffects } from '../../types/index';
import ImageUploader from './ImageUploader';
import ThemeSelector from './ThemeSelector';
import ImageEffectsPanel from './ImageEffects';
import { useThemeContext } from '../../contexts/ThemeContext';
import { themeBackgrounds } from '../../data/themeBackgrounds';

interface BackgroundImagePanelProps {
  config: BackgroundImageConfig;
  onChange: (config: BackgroundImageConfig) => void;
  disabled?: boolean;
  onFontColorSuggestion?: (textColor: string) => void; // 
  cardWidth?: number; // 
  cardHeight?: number; // 
}

const BackgroundImagePanel: React.FC<BackgroundImagePanelProps> = ({
  config,
  onChange,
  disabled = false,
  onFontColorSuggestion,
  cardWidth = 400,
  cardHeight = 600
}) => {
  const { t } = useThemeContext();
  // useEffect
  const [selectedThemeImage, setSelectedThemeImage] = useState<ThemeImage | null>(null);

  // 
  const shouldShowDisplaySettings = useMemo(() => {
    const sourceType = config.source.type;
    const hasUploadedImage = config.source.uploadedImage?.url;
    const hasThemeImage = config.source.themeImage?.path;
    
    let hasValidSource;
    let willShowDisplaySettings;
    
    if (sourceType === 'upload') {
      hasValidSource = hasUploadedImage;
      willShowDisplaySettings = hasValidSource;
    } else if (sourceType === 'theme') {
      hasValidSource = hasThemeImage;
      willShowDisplaySettings = hasValidSource;
    } else {
      hasValidSource = false;
      willShowDisplaySettings = false;
    }
    
    return willShowDisplaySettings;
  }, [config.source]);

  // 
  useEffect(() => {
    if (!config.enabled || !shouldShowDisplaySettings) {
      return;
    }
    
    // 
    if (config.source.type === 'theme' && config.source.themeImage?.themeId && config.source.themeImage?.variantId) {
      // 
      const theme = themeBackgrounds.find(t => t.id === config.source.themeImage?.themeId);
      const variant = theme?.variants.find(v => v.id === config.source.themeImage?.variantId);
      
      if (variant?.textColor) {
        onFontColorSuggestion?.(variant.textColor);
      }
    }
  }, [config, shouldShowDisplaySettings, onFontColorSuggestion]);

  const handleEnabledChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange({
      ...config,
      enabled: event.target.checked
    });
  };

  const handleSourceTypeChange = (
    _event: React.MouseEvent<HTMLElement>,
    newType: 'upload' | 'theme'
  ) => {
    if (newType !== null) {
      if (newType === 'upload') {
        onChange({
          ...config,
          source: {
            type: 'upload',
            uploadedImage: config.source.uploadedImage
          }
        });
      } else if (newType === 'theme') {
        onChange({
          ...config,
          source: {
            type: 'theme',
            themeImage: config.source.themeImage
          }
        });
      }
    }
  };

  const handleImageUpload = (image: UploadedImage) => {
    onChange({
      ...config,
      enabled: true, // 
      source: {
        type: 'upload' as const,
        uploadedImage: image
      }
    });
  };

  const handleImageRemove = () => {
    onChange({
      ...config,
      source: {
        type: 'upload',
        uploadedImage: undefined
      }
    });
  };

  const handleThemeSelect = (themeImage: ThemeImage) => {
    const newConfig = {
      ...config,
      enabled: true, // 
      source: {
        type: 'theme' as const,
        themeImage
      }
    };
    
    // useEffect
    setSelectedThemeImage(themeImage);
    
    // 
    onChange(newConfig);
  };

  const handleDisplayChange = (property: string, value: any) => {
    onChange({
      ...config,
      display: {
        ...config.display,
        [property]: value
      }
    });
  };

  const handleEffectsChange = (effects: ImageEffects) => {
    onChange({
      ...config,
      effects
    });
  };

  return (
    <Box>
      
      {/*  */}
      <FormControlLabel
        control={
          <Switch
            checked={config.enabled}
            onChange={handleEnabledChange}
            disabled={disabled}
          />
        }
        label={t('enableBackground') || ''}
        sx={{ mb: 2 }}
      />

      {config.enabled && (
        <>
          {/*  */}
          <Box sx={{ mb: 3 }}>
            <Typography variant="body2" gutterBottom sx={{ mb: 1 }}>
              {t('imageSource') || ''}
            </Typography>
            <ToggleButtonGroup
              value={config.source.type}
              exclusive
              onChange={handleSourceTypeChange}
              size="small"
              fullWidth
              disabled={disabled}
            >
              <ToggleButton value="upload" sx={{ textTransform: 'none' }}>
                <UploadIcon sx={{ mr: 1, fontSize: 16 }} />
                {t('uploadImage') || ''}
              </ToggleButton>
              <ToggleButton value="theme" sx={{ textTransform: 'none' }}>
                <PaletteIcon sx={{ mr: 1, fontSize: 16 }} />
                {t('themeBackground') || ''}
              </ToggleButton>
            </ToggleButtonGroup>
          </Box>

          {/*  */}
          {config.source.type === 'upload' && (
            <Box sx={{ mb: 3 }}>
              <ImageUploader
                onImageUpload={handleImageUpload}
                onImageRemove={handleImageRemove}
                currentImage={config.source.uploadedImage}
                disabled={disabled}
              />
            </Box>
          )}

          {/*  */}
          {config.source.type === 'theme' && (
            <Box sx={{ mb: 3 }}>
              <ThemeSelector
                onThemeSelect={handleThemeSelect}
                selectedThemeId={config.source.themeImage?.themeId}
                selectedVariantId={config.source.themeImage?.variantId}
                disabled={disabled}
                onFontColorSuggestion={onFontColorSuggestion}
                cardWidth={cardWidth}
                cardHeight={cardHeight}
              />
            </Box>
          )}

          {/*  */}
          {shouldShowDisplaySettings && (
            <>
              <Divider sx={{ my: 3 }} />
              
              <Typography variant="body2" gutterBottom sx={{ mb: 2 }}>
                {t('displaySettings') || ''}
              </Typography>

              {/*  */}
              <Box sx={{ mb: 3 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                  <Typography variant="body2" sx={{ flexGrow: 1 }}>
                    {t('opacity') || ''}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {Math.round(config.display.opacity * 100)}%
                  </Typography>
                </Box>
                <Slider
                  value={config.display.opacity}
                  onChange={(_, value) => handleDisplayChange('opacity', value)}
                  min={0}
                  max={1}
                  step={0.1}
                  disabled={disabled}
                  sx={{
                    '& .MuiSlider-thumb': {
                      width: 16,
                      height: 16
                    }
                  }}
                />
              </Box>

              {/*  */}
              <Box sx={{ mb: 3 }}>
                <FormControl fullWidth size="small">
                  <InputLabel>{t('blendMode') || ''}</InputLabel>
                  <Select
                    value={config.display.blendMode}
                    label={t('blendMode') || ''}
                    onChange={(e) => handleDisplayChange('blendMode', e.target.value)}
                    disabled={disabled}
                  >
                    <MenuItem value="normal">{t('normal') || ''}</MenuItem>
                    <MenuItem value="multiply">{t('multiply') || ''}</MenuItem>
                    <MenuItem value="screen">{t('screen') || ''}</MenuItem>
                    <MenuItem value="overlay">{t('overlay') || ''}</MenuItem>
                    <MenuItem value="soft-light">{t('softLight') || ''}</MenuItem>
                  </Select>
                </FormControl>
              </Box>

              {/*  */}
              <Box sx={{ mb: 3 }}>
                <FormControl fullWidth size="small">
                  <InputLabel>{t('size') || ''}</InputLabel>
                  <Select
                    value={config.display.size}
                    label={t('size') || ''}
                    onChange={(e) => handleDisplayChange('size', e.target.value)}
                    disabled={disabled}
                  >
                    <MenuItem value="cover">{t('cover') || ''}{t('mayBeCropped') || ''}</MenuItem>
                    <MenuItem value="contain">{t('contain') || ''}{t('completeDisplay') || ''}</MenuItem>
                    <MenuItem value="stretch">{t('stretch') || ''}{t('mayBeDeformed') || ''}</MenuItem>
                    <MenuItem value="auto">{t('auto') || ''}</MenuItem>
                  </Select>
                </FormControl>
              </Box>

              {/*  */}
              <Box sx={{ mb: 3 }}>
                <FormControl fullWidth size="small">
                  <InputLabel>{t('position') || ''}</InputLabel>
                  <Select
                    value={config.display.position}
                    label={t('position') || ''}
                    onChange={(e) => handleDisplayChange('position', e.target.value)}
                    disabled={disabled}
                  >
                    <MenuItem value="center">{t('center') || ''}</MenuItem>
                    <MenuItem value="top">{t('top') || ''}</MenuItem>
                    <MenuItem value="bottom">{t('bottom') || ''}</MenuItem>
                    <MenuItem value="left">{t('left') || ''}</MenuItem>
                    <MenuItem value="right">{t('right') || ''}</MenuItem>
                  </Select>
                </FormControl>
              </Box>

              {/*  */}
              <Box sx={{ mb: 3 }}>
                <FormControl fullWidth size="small">
                  <InputLabel>{t('repeat') || ''}</InputLabel>
                  <Select
                    value={config.display.repeat}
                    label={t('repeat') || ''}
                    onChange={(e) => handleDisplayChange('repeat', e.target.value)}
                    disabled={disabled}
                  >
                    <MenuItem value="no-repeat">{t('noRepeat') || ''}</MenuItem>
                    <MenuItem value="repeat">{t('repeat') || ''}</MenuItem>
                    <MenuItem value="repeat-x">{t('repeatX') || ''}</MenuItem>
                    <MenuItem value="repeat-y">{t('repeatY') || ''}</MenuItem>
                  </Select>
                </FormControl>
              </Box>

              {/*  */}
              <ImageEffectsPanel
                effects={config.effects}
                onChange={handleEffectsChange}
                disabled={disabled}
              />
            </>
          )}
        </>
      )}
    </Box>
  );
};

export default BackgroundImagePanel; 