import React, { useCallback, useState } from 'react';
import {
  Box,
  Typography,
  Paper,
  LinearProgress,
  Alert,
  Button,
  Chip
} from '@mui/material';
import {
  CloudUpload as CloudUploadIcon,
  Image as ImageIcon,
  Delete as DeleteIcon
} from '@mui/icons-material';
import { validateImage, formatFileSize } from '../../utils/imageValidation';
import { UploadedImage } from '../../types/index';
import { useThemeContext } from '../../contexts/ThemeContext';

interface ImageUploaderProps {
  onImageUpload: (image: UploadedImage) => void;
  onImageRemove: () => void;
  currentImage?: UploadedImage | null;
  disabled?: boolean;
}

const ImageUploader: React.FC<ImageUploaderProps> = ({
  onImageUpload,
  onImageRemove,
  currentImage,
  disabled = false
}) => {
  const { t } = useThemeContext();
  const [isDragOver, setIsDragOver] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // processFileuseCallback
  const processFile = useCallback(async (file: File) => {
    setError(null);
    setUploading(true);

    try {
      // 
      const validation = await validateImage(file);
      if (!validation.isValid) {
        setError(validation.error || '');
        return;
      }

      // Data URLBlob URL
      // html2canvas
      const dataUrl = await convertFileToDataUrl(file);
      
      // 
      const uploadedImage: UploadedImage = {
        file,
        url: dataUrl, // Data URLBlob URL
        name: file.name,
        size: file.size
      };

      onImageUpload(uploadedImage);
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setUploading(false);
    }
  }, [onImageUpload]);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    if (!disabled) {
      setIsDragOver(true);
    }
  }, [disabled]);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  }, []);

  const handleDrop = useCallback(async (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
    
    if (disabled) return;

    const files = Array.from(e.dataTransfer.files);
    if (files.length > 0) {
      await processFile(files[0]);
    }
  }, [disabled, processFile]);

  const handleFileSelect = useCallback(async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files[0]) {
      await processFile(files[0]);
    }
    // input
    e.target.value = '';
  }, [processFile]);

  // Data URL
  const convertFileToDataUrl = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = (event) => {
        const result = event.target?.result;
        if (typeof result === 'string') {
          resolve(result);
        } else {
          reject(new Error(''));
        }
      };
      
      reader.onerror = () => {
        reject(new Error(''));
      };
      
      reader.readAsDataURL(file);
    });
  };

  const handleRemoveImage = () => {
    if (currentImage) {
      // Data URL
      onImageRemove();
    }
  };

  // 
  if (currentImage) {
    return (
      <Paper
        elevation={1}
        sx={{
          p: 2,
          textAlign: 'center',
          border: '1px solid',
          borderColor: 'primary.main',
          backgroundColor: 'background.paper'
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <ImageIcon sx={{ mr: 1, color: 'primary.main' }} />
          <Typography variant="body2" sx={{ flexGrow: 1, textAlign: 'left' }}>
            {currentImage.name}
          </Typography>
          <Chip
            label={formatFileSize(currentImage.size)}
            size="small"
            variant="outlined"
            sx={{ ml: 1 }}
          />
        </Box>
        
        <Box sx={{ mb: 2 }}>
          <img
            src={currentImage.url}
            alt={t('uploadedBackground') || ''}
            style={{
              maxWidth: '100%',
              maxHeight: '120px',
              borderRadius: '8px',
              objectFit: 'cover'
            }}
          />
        </Box>
        
        <Button
          variant="outlined"
          color="error"
          size="small"
          startIcon={<DeleteIcon />}
          onClick={handleRemoveImage}
          disabled={disabled}
        >
          {t('removeImage') || ''}
        </Button>
      </Paper>
    );
  }

  return (
    <Box>
      <Paper
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        elevation={isDragOver ? 4 : 1}
        sx={{
          p: 3,
          textAlign: 'center',
          cursor: disabled ? 'not-allowed' : 'pointer',
          border: '2px dashed',
          borderColor: isDragOver ? 'primary.main' : 'grey.300',
          backgroundColor: isDragOver ? 'action.hover' : 'background.paper',
          transition: 'all 0.2s ease',
          opacity: disabled ? 0.6 : 1,
          '&:hover': {
            borderColor: disabled ? 'grey.300' : 'primary.main',
            backgroundColor: disabled ? 'background.paper' : 'action.hover'
          }
        }}
      >
        {uploading ? (
          <Box>
            <LinearProgress sx={{ mb: 2 }} />
            <Typography variant="body2" color="text.secondary">
              {t('processingImage') || '...'}
            </Typography>
          </Box>
        ) : (
          <>
            <CloudUploadIcon
              sx={{
                fontSize: 48,
                color: 'primary.main',
                mb: 1
              }}
            />
            <Typography variant="h6" gutterBottom>
              {isDragOver ? (t('dropFileHere') || '') : (t('uploadImageHere') || '')}
            </Typography>
            <Typography variant="caption" color="text.secondary" display="block" sx={{ mb: 2 }}>
              {t('supportedFormats') || 'JPEGPNGWebPSVG10MB'}
            </Typography>
            
            <input
              type="file"
              accept="image/jpeg,image/png,image/webp,image/svg+xml"
              onChange={handleFileSelect}
              disabled={disabled}
              style={{ display: 'none' }}
              id="image-upload-input"
            />
            <label htmlFor="image-upload-input">
              <Button
                variant="contained"
                component="span"
                disabled={disabled}
                startIcon={<CloudUploadIcon />}
              >
                {t('selectFile') || ''}
              </Button>
            </label>
          </>
        )}
      </Paper>

      {error && (
        <Alert severity="error" sx={{ mt: 2 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
};

export default ImageUploader; 