//  - Material-UI
import html2canvas from 'html2canvas';
import { CardConfig, ExportOptions } from '../types/index';

// Canvas
const CANVAS_SETTINGS = {
  allowTaint: false,
  useCORS: true,
  scale: 1,
  logging: false,
  scrollX: 0,
  scrollY: 0,
  foreignObjectRendering: false,
  removeContainer: true,
  backgroundColor: null
} as const;

// 
class ImageCache {
  private static cache = new Map<string, string>();
  
  static async getOrCache(src: string): Promise<string> {
    if (this.cache.has(src)) {
      return this.cache.get(src)!;
    }
    
    try {
      const response = await fetch(src);
      if (!response.ok) throw new Error(`HTTP ${response.status}`);
      
      const blob = await response.blob();
      const dataUrl = await new Promise<string>((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result as string);
        reader.onerror = reject;
        reader.readAsDataURL(blob);
      });
      
      this.cache.set(src, dataUrl);
      return dataUrl;
    } catch (error) {
      console.warn(`: ${src}`, error);
      return src;
    }
  }
  
  static clear(): void {
    this.cache.clear();
  }
}

// 
const preloadAndCacheImages = async (element: HTMLElement): Promise<void> => {
  const images = Array.from(element.querySelectorAll('img'));
  const bgImageElements = Array.from(element.querySelectorAll('*')).filter(el => {
    const computedStyle = window.getComputedStyle(el);
    const bgImage = computedStyle.backgroundImage;
    return bgImage && bgImage !== 'none' && bgImage.includes('url(');
  });
  
  const totalElements = images.length + bgImageElements.length;
  
  if (totalElements === 0) {
    return;
  }
  
  const pendingImages: Promise<void>[] = [];
  
  // img
  images.forEach((img, index) => {
    if (img.src && !img.src.startsWith('data:') && !img.src.startsWith('blob:')) {
      const promise = ImageCache.getOrCache(img.src).then(dataUrl => {
        img.src = dataUrl;
      }).catch(error => {
        console.warn(` ${index + 1} :`, error);
      });
      pendingImages.push(promise);
    }
  });
  
  // 
  bgImageElements.forEach((element, index) => {
    const computedStyle = window.getComputedStyle(element);
    const bgImage = computedStyle.backgroundImage;
    const urlMatch = bgImage.match(/url\(['"]?([^'"]+)['"]?\)/);
    
    if (urlMatch && urlMatch[1] && !urlMatch[1].startsWith('data:') && !urlMatch[1].startsWith('blob:')) {
      const promise = ImageCache.getOrCache(urlMatch[1]).then(dataUrl => {
        (element as HTMLElement).style.backgroundImage = `url("${dataUrl}")`;
      }).catch(error => {
        console.warn(` ${index + 1} :`, error);
      });
      pendingImages.push(promise);
    }
  });
  
  if (pendingImages.length > 0) {
    try {
      await Promise.all(pendingImages);
    } catch (error) {
      console.warn(':', error);
    }
  }
};

// 
const waitForImageLoad = async (element: HTMLElement): Promise<void> => {
  const images = Array.from(element.querySelectorAll('img'));
  
  if (images.length === 0) {
    return;
  }
  
  const imagePromises = images.map(img => {
    return new Promise<void>((resolve) => {
      if (img.complete && img.naturalHeight !== 0) {
        resolve();
      } else {
        img.addEventListener('load', () => resolve(), { once: true });
        img.addEventListener('error', () => resolve(), { once: true });
        
        // 5
        setTimeout(resolve, 5000);
      }
    });
  });
  
  await Promise.all(imagePromises);
};

// 
const waitForAllImages = async (element: HTMLElement): Promise<void> => {
  // 1: 
  await preloadAndCacheImages(element);
  
  // 2: 
  await waitForImageLoad(element);
  
  // 3: 
  await new Promise(resolve => requestAnimationFrame(resolve));
};

// 
const applyOtherExportStylesSync = (
  clonedElement: HTMLElement, 
  cardConfig: CardConfig,
  cardWidth: number,
  cardHeight: number
): void => {
  // 
  
  // 
  if (!cardConfig.backgroundImage.enabled) {
    return;
  }
  
  // URL
  const backgroundImageUrl = cardConfig.backgroundImage.source.type === 'upload' 
    ? cardConfig.backgroundImage.source.uploadedImage?.url
    : cardConfig.backgroundImage.source.type === 'theme' 
    ? cardConfig.backgroundImage.source.themeImage?.path
    : null;
  
  if (!backgroundImageUrl) {
    return;
  }
  
  // 
  const backgroundElements: HTMLElement[] = [];
  
  // div
  const allDivs = Array.from(clonedElement.querySelectorAll('div'));
  allDivs.forEach(div => {
    const computedStyle = window.getComputedStyle(div);
    const bgImage = computedStyle.backgroundImage;
    
    // stats-box
    if (div.className.includes('stats-box')) {
      return;
    }
    
    // MUI Box
    if (div.className.includes('MuiBox-root') || 
        (bgImage && bgImage !== 'none' && bgImage.includes('url('))) {
      backgroundElements.push(div);
    }
  });
  
  // 
  // 
  if (clonedElement.style.position !== 'relative') {
    clonedElement.style.position = 'relative';
  }
  
  // 
  const statsElements = Array.from(clonedElement.querySelectorAll('*')).filter(el => 
    el.className.includes('stats-box') || 
    (el.className.includes('css-') && (el as HTMLElement).style.position === 'absolute')
  );
  
  statsElements.forEach(statsEl => {
    const element = statsEl as HTMLElement;
    const computedStyle = window.getComputedStyle(element);
    
    // 
    const currentBottom = computedStyle.bottom;
    const currentRight = computedStyle.right;
    const currentTop = computedStyle.top;
    const currentLeft = computedStyle.left;
    
    // 
    const expectedBottom = '20px';
    const expectedRight = '20px';
    
    // 
    const hasTopValue = currentTop && currentTop !== 'auto' && currentTop !== '0px';
    const hasLeftValue = currentLeft && currentLeft !== 'auto' && currentLeft !== '0px';
    const bottomMatches = currentBottom === expectedBottom;
    const rightMatches = currentRight === expectedRight;
    const positionCorrect = !hasTopValue && !hasLeftValue && bottomMatches && rightMatches;
    
    if (!positionCorrect) {
      // 
      element.style.position = 'absolute !important';
      element.style.bottom = '20px !important';
      element.style.right = '20px !important';
      element.style.top = 'auto !important';
      element.style.left = 'auto !important';
      element.style.transform = 'none !important';
      element.style.zIndex = '10';
      
      // conflicting CSS
      if (element.className.includes('css-')) {
        // Material-UI
        element.style.cssText += `
          position: absolute !important;
          bottom: 20px !important;
          right: 20px !important;
          top: auto !important;
          left: auto !important;
          transform: none !important;
        `;
      }
      
      // DOM
      const currentStyle = element.getAttribute('style') || '';
      const newStyle = currentStyle + 
        '; position: absolute !important' +
        '; bottom: 20px !important' +
        '; right: 20px !important' +
        '; top: auto !important' +
        '; left: auto !important' +
        '; transform: none !important';
      element.setAttribute('style', newStyle);
    }
  });
  
  // 
  const textColor = cardConfig.textColor;
  if (textColor) {
    // 
    const textElements = Array.from(clonedElement.querySelectorAll('*')).filter(el => {
      const tagName = el.tagName.toLowerCase();
      return ['p', 'span', 'div', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'typography'].includes(tagName) ||
             el.className.includes('MuiTypography') ||
             el.className.includes('MuiChip-label') ||
             el.textContent && el.textContent.trim().length > 0;
    });
    
    textElements.forEach(el => {
      const element = el as HTMLElement;
      // !important
      element.style.color = `${textColor} !important`;
      
      // Material-UI
      if (element.className.includes('MuiTypography-root') || 
          element.className.includes('MuiChip-label')) {
        element.style.cssText += `color: ${textColor} !important;`;
      }
    });
  }
  
  // 
  const totalStatsElements = Array.from(clonedElement.querySelectorAll('*')).filter(el => 
    el.textContent?.includes('') || 
    el.className.includes('stats-box')
  );
  
  totalStatsElements.forEach(el => {
    const element = el as HTMLElement;
    
    // 
    const isDarkBackground = cardConfig.backgroundColor === '#1a1a2e' || cardConfig.textColor === '#ffffff';
    
    if (isDarkBackground) {
      // 
      element.style.background = 'linear-gradient(135deg, rgba(255,255,255,0.03), rgba(255,255,255,0.08)) !important';
    } else {
      // 
      element.style.background = 'linear-gradient(135deg, rgba(0,0,0,0.03), rgba(0,0,0,0.08)) !important';
    }
    
    // backdrop-filter
    element.style.backdropFilter = 'blur(10px) !important';
    (element.style as any).webkitBackdropFilter = 'blur(10px) !important';
    
    // 
    element.style.border = `1px solid ${isDarkBackground ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)'} !important`;
  });
};

// 
export const exportCard = async (
  element: HTMLElement, 
  cardConfig: CardConfig,
  exportOptions: ExportOptions,
  onProgress?: (progress: number) => void
): Promise<void> => {
  // 
  
  try {
    // 1: 
    onProgress?.(10);
    await waitForAllImages(element);
    
    onProgress?.(30);
    
    // 
    const rect = element.getBoundingClientRect();
    const elementWidth = rect.width || cardConfig.width;
    const elementHeight = rect.height || 450;
    
    // 
    const scale = exportOptions.scale;
    const finalWidth = Math.round(elementWidth * scale);
    const finalHeight = Math.round(elementHeight * scale);
    
    onProgress?.(50);
    
    // 3: html2canvas
    const canvas = await html2canvas(element, {
      ...CANVAS_SETTINGS,
      width: elementWidth,
      height: elementHeight,
      scale: scale,
      onclone: (clonedDocument, clonedElement) => {
        // 
        applyOtherExportStylesSync(clonedElement, cardConfig, elementWidth, elementHeight);
      }
    });
    
    onProgress?.(80);
    
    // 4: 
    const format = exportOptions.format;
    const quality = exportOptions.quality / 100;
    
    let dataUrl: string;
    if (format === 'jpeg') {
      dataUrl = canvas.toDataURL('image/jpeg', quality);
      } else {
      dataUrl = canvas.toDataURL('image/png');
    }
    
    // 
    const link = document.createElement('a');
    link.download = `media-card-${Date.now()}.${format}`;
    link.href = dataUrl;
    
    // 
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    onProgress?.(100);
    
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

// 
export const getExportPreview = async (
  element: HTMLElement,
  cardConfig: CardConfig,
  exportOptions: ExportOptions
): Promise<string> => {
  try {
    await waitForAllImages(element);
    
    const rect = element.getBoundingClientRect();
    const elementWidth = rect.width || cardConfig.width;
    const elementHeight = rect.height || 450;
    
    const canvas = await html2canvas(element, {
      ...CANVAS_SETTINGS,
      width: elementWidth,
      height: elementHeight,
      scale: 1, // 1x
      onclone: (clonedDocument, clonedElement) => {
        applyOtherExportStylesSync(clonedElement, cardConfig, elementWidth, elementHeight);
      }
    });
    
    return canvas.toDataURL('image/png');
  } catch (error) {
    console.error(':', error);
    throw error;
  }
}; 