/**
 * favicon
 */
export const createFavicon = (isDarkMode: boolean = false) => {
  // 
  const canvas = document.createElement('canvas');
  canvas.width = 64;
  canvas.height = 64;
  const ctx = canvas.getContext('2d');
  
  if (!ctx) return;
  
  // 
  const primaryColor = isDarkMode ? '#90caf9' : '#1976D2';
  const secondaryColor = isDarkMode ? '#ce93d8' : '#9c27b0';
  const shadowColor = isDarkMode ? 'rgba(144, 202, 249, 0.4)' : 'rgba(0, 0, 0, 0.3)';
  
  // 
  const bgGradient = ctx.createLinearGradient(0, 0, 64, 64);
  bgGradient.addColorStop(0, primaryColor);
  bgGradient.addColorStop(1, secondaryColor);
  
  // 
  ctx.shadowColor = shadowColor;
  ctx.shadowBlur = 6;
  ctx.shadowOffsetX = 0;
  ctx.shadowOffsetY = 3;
  
  // 
  ctx.fillStyle = bgGradient;
  ctx.beginPath();
  ctx.roundRect(0, 0, 64, 64, 16);
  ctx.fill();
  
  // 
  ctx.shadowBlur = 0;
  ctx.shadowOffsetX = 0;
  ctx.shadowOffsetY = 0;
  
  // 
  ctx.fillStyle = isDarkMode ? 'rgba(255, 255, 255, 0.8)' : 'rgba(255, 255, 255, 0.9)';
  ctx.beginPath();
  ctx.roundRect(12, 12, 40, 40, 10);
  ctx.fill();
  
  // 
  const innerGradient = ctx.createLinearGradient(20, 20, 44, 44);
  innerGradient.addColorStop(0, primaryColor);
  innerGradient.addColorStop(1, secondaryColor);
  
  // 
  ctx.fillStyle = innerGradient;
  ctx.beginPath();
  ctx.roundRect(20, 20, 24, 24, 6);
  ctx.fill();
  
  // 
  ctx.fillStyle = 'rgba(255, 255, 255, 0.3)';
  ctx.beginPath();
  ctx.roundRect(20, 20, 24, 12, [6, 6, 0, 0]);
  ctx.fill();
  
  // 
  ctx.fillStyle = 'white';
  ctx.beginPath();
  ctx.arc(32, 32, 5, 0, Math.PI * 2);
  ctx.fill();
  
  // 
  ctx.fillStyle = 'rgba(255, 255, 255, 0.8)';
  ctx.beginPath();
  ctx.arc(30, 30, 2, 0, Math.PI * 2);
  ctx.fill();
  
  // URL
  const dataUrl = canvas.toDataURL('image/png');
  
  // favicon
  let link = document.querySelector("link[rel='icon']") as HTMLLinkElement;
  
  if (!link) {
    link = document.createElement('link');
    link.rel = 'icon';
    document.head.appendChild(link);
  }
  
  link.href = dataUrl;
  
  // SafariiOS
  let appleLink = document.querySelector("link[rel='apple-touch-icon']") as HTMLLinkElement;
  
  if (!appleLink) {
    appleLink = document.createElement('link');
    appleLink.rel = 'apple-touch-icon';
    document.head.appendChild(appleLink);
  }
  
  appleLink.href = dataUrl;
}; 